<template>
   <div class="patrimonio-tabela">
		<TabelaGeralFiltro :aba="aba" />
		<div class="tabela">
			<va-data-table hoverable :items="getObjetos" :columns="columns">
				<!-- GERAL -->
				<template #cell(description)="{ rowIndex }">
					{{getStringSlice(getObjetos[rowIndex].description)}}
				</template>
				<template #cell(created_at)="{ rowIndex }">
					{{ getObjetos[rowIndex].is_model ? '' : timestamp(getObjetos[rowIndex].created_at)}}
				</template>
				<template #cell(updated_at)="{ rowIndex }">
					{{timestamp(getObjetos[rowIndex].updated_at)}}
				</template>
				<template #cell(actions)="{ rowIndex }">
					<TabelaGeralActionButtons :objeto="getObjetos[rowIndex]" />
				</template>
				<!-- ATIVOS -->
				<template v-if="aba.nome == 'Ativos'" #cell(coordinates)="{ rowIndex }">
					<va-button v-if="getObjetos[rowIndex].coordinates" color="secondary" flat icon="place" @click=maps(getObjetos[rowIndex].coordinates.latitude,getObjetos[rowIndex].coordinates.longitude) size="small" />
				</template>
				<template v-if="aba.nome == 'Ativos'" #cell(parent_ativo)="{ rowIndex }">
					{{getObjetos[rowIndex].parent_ativo ? getObjetos[rowIndex].parent_ativo.description : ''}}	
				</template>
				<!-- REPORTS -->
				<template v-if="aba.nome == 'Reports'" #cell(anomaly)="{ rowIndex }">
					{{getStringSlice(getObjetos[rowIndex].anomaly)}}
				</template>
				<template v-if="aba.nome == 'Reports'" #cell(is_model)="{ rowIndex }">
					{{getObjetos[rowIndex].is_model ? 'Modelo' : 'Comum'}}
				</template>
				<template v-if="aba.nome == 'Reports' || aba.nome == 'Ordens de Serviço' || aba.nome == 'Inspeções' || aba.nome == 'Chamados'" #cell(ativo)="{ rowIndex }">
					{{getObjetos[rowIndex].ativo ? getStringSlice(getObjetos[rowIndex].ativo.description) : ''}}
				</template>
				<template v-if="aba.nome == 'Reports'" #cell(criticidade)="{ rowIndex }">
					{{getObjetos[rowIndex].criticidade.slice(2)}}
				</template>
				<template v-if="aba.nome == 'Reports' || aba.nome == 'Chamados'" #cell(finished)="{ rowIndex }">
					<va-icon size="medium" :name="getObjetos[rowIndex].finished? 'done' : 'close'" :color="getObjetos[rowIndex].finished? 'verde' : 'laranja'"/>
				</template>
				<!-- SERVICOS -->
				<template v-if="aba.nome == 'Ordens de Serviço'" #cell(responsible)="{ rowIndex }">
					{{getObjetos[rowIndex].responsible ? getObjetos[rowIndex].responsible.name: ''}}
				</template>
				<template v-if="aba.nome == 'Ordens de Serviço'" #cell(request)="{ rowIndex }">
					{{getStringSlice(getObjetos[rowIndex].request)}}
				</template>
				<!-- MANTENEDORES -->
				<template v-if="aba.nome == 'Mantenedores'" #cell(source)="{ rowIndex }">
					{{getObjetos[rowIndex].source == 1 ? "Externo": getObjetos[rowIndex].source == 0 ? "Interno" : ""}}
				</template>
				<template v-if="aba.nome == 'Mantenedores'" #cell(coust_hour)="{ rowIndex }">
					R${{getObjetos[rowIndex].coust_hour}}
				</template>
				<!-- MEMBROS -->
				<template v-if="aba.nome == 'Membros' || aba.nome == 'Controle de Usuários'" #cell(level_permission)="{ rowIndex }">
					<span v-if="aba.nome == 'Membros'">{{getObjetos[rowIndex].level_permission == 0 ? "Administrador": getObjetos[rowIndex].level_permission == 1 ? "Programador" :  getObjetos[rowIndex].level_permission == 2 ?  "Membro": getObjetos[rowIndex].level_permission == 100 ?  "Convidado" : getObjetos[rowIndex].level_permission == 101 ?  "Convidado Oculto":  ""}}</span>
					<span v-if="aba.nome == 'Controle de Usuários'">{{getObjetos[rowIndex].level_permission == 0 ? "Administrador master": getObjetos[rowIndex].level_permission == 1 ? "Administrador secundário" : getObjetos[rowIndex].level_permission >= 100 ?  "Usuário Comum" : ""}}</span>
				</template>
				<!-- RELATORIOS -->
				<template v-if="aba.nome == 'Relatórios'" #cell(actions_relatorios)="{ }">
					<va-button flat color="secondary" size="small" icon="download" ></va-button>
				</template>
				<!-- INSPEÇÕES -->
				<template v-if="aba.nome == 'Inspeções'" #cell(checklist)="{ rowIndex }">
					{{getObjetos[rowIndex].checklist ? getObjetos[rowIndex].checklist.description : ''}}
				</template>
				<!-- CHAMADOS -->
				<template v-if="aba.nome == 'Chamados'" #cell(created_by)="{ rowIndex }">
					{{getObjetos[rowIndex].user ? getObjetos[rowIndex].user.name: ''}} 
				</template>
				<template v-if="aba.nome == 'Chamados'" #cell(assigned_to)="{ rowIndex }">
					{{getObjetos[rowIndex].assigned ? getObjetos[rowIndex].assigned.name: ''}}
				</template>
				<!-- ADMIN -->
				<template v-if="aba.tipo == 'admin-categorias'" #cell(base)="{ rowIndex }">
					<va-icon :name="getObjetos[rowIndex].base == 'serviços' ? 'construction' : 'tablet_mac'" size="small"></va-icon>
					{{capitalizeFirstLetter(getObjetos[rowIndex].base)}}
				</template>
				<template v-if="aba.tipo == 'admin-categorias'" #cell(public)="{ rowIndex }">
					{{getObjetos[rowIndex].public ? 'Público' : 'Privado'}}
				</template>
				<template v-if="['admin-categorias', 'admin-empresas'].includes(aba.tipo)" #cell(segment_id)="{ rowIndex }">
					{{getSegmento(getObjetos[rowIndex].segment_id)}}
				</template>

				<template #cell(actions_membros)="{ rowIndex }">
					<div class="actions-buttons" v-if="getPermissaoMembro == 0 || getPermissaoUsuario == 0">
						<va-button color="secondary" flat icon="edit" @click="update(getObjetos[rowIndex])" size="small" />
					</div>
				</template>
				
				<template #cell(actions_usuarios)="{ rowIndex }">
					<div class="actions-buttons" v-if="getPermissaoUsuario <= 1">
						<va-button color="secondary" flat icon="edit" @click="update(getObjetos[rowIndex])" size="small" />
					</div>
				</template>
			</va-data-table>
		</div>
		<div class="buttom-space" v-if="getObjetos.length > 7" ></div>
		
	  	<!-- <div v-for="objeto in getObjetos" :key="objeto.id">
			<div style="padding: 10px;">{{objeto}}</div>
		</div>  -->
   </div>
</template>

<script>
import TabelaGeralFiltro from './TabelaGeralFiltro'
import TabelaGeralActionButtons from './TabelaGeralActionButtons'
export default {
    name: 'TabelaGeral',
	components: {TabelaGeralFiltro, TabelaGeralActionButtons, },
	props : ['aba', 'caption', 'objetos'],
	data () {
		return {
			value: true,
			objeto: {},
    	}
	},
	computed: {
		getSegmentos(){
			return this.$store.getters['admin.getSegmentos']
		},
		getPermissaoMembro(){
			return this.$store.getters['membro.getPermissao']
		},
		getPermissaoUsuario(){
			return this.$store.getters['usuario.get'].level_permission
		},
		getUsuario(){
			return this.$store.getters['usuario.get'].level_permission
		},
		getObjetos(){
			const objs = this.objetos.map((obj)=>{
				if (this.aba.nome == 'Ativos') {
					obj.categoria = obj.category ? obj.category.description : ''
				}
				else if (this.aba.nome == 'Inspeções' || this.aba.nome == 'Reports'){
					if (obj.is_model && this.aba.nome == 'Reports'){
						obj.sgs = ''
					}else{
						obj.sgs = obj.canceled == 1 ? 'Cancelado' : obj.finished_at == null  ? 'Em aberto' : 'Concluído'
					}
				}
				else if (this.aba.nome == 'Chamados'){
					obj.sgs = obj.approved == null ? 'Em aberto' : obj.approved == 0 ? 'Rejeitado' : obj.finished == 0 ? 'Em andamento' : 'Concluído'
				}
				else if (this.aba.nome == 'Ordens de Serviço'){
					obj.sgs = obj.canceled == 1? 'Cancelado' : obj.finished_approved ==1 ? 'Concluído' : obj.finish == 1? 'Enviado para conclusão' :  obj.dt_end_scheduled == null?  'Não programado': new Date(obj.dt_end_scheduled) > new Date() ? 'Em Aberto': 'Atrasado'
					if(obj.dt_start_scheduled != null || obj.dt_end_scheduled != null){
						obj.inicio =  new Date(obj.dt_start_scheduled)
						obj.inicio = obj.inicio.toLocaleString('pt-BR')
						obj.fim =  new Date(obj.dt_end_scheduled)
						obj.fim = obj.fim.toLocaleString('pt-BR')
					}
					else{
						obj.inicio = ''
						obj.fim =  ''
					}
				}
				else if (['Checklists', 'Planos'].includes(this.aba.nome)){
					obj.privacidade = obj.public ? 'Público' : 'Privado'
					obj.tipo = obj.type == 1 ? 'Manut.' : obj.type == 2 ? 'Oper.' : 'Cont.'
				}
				return obj
			})
			return objs
		},
		columns(){
			if(this.aba.columns) return this.aba.columns
			return []
		}
	},
	methods:{
		getSegmento(id){
			const array = this.getSegmentos.filter(e=>e.id==id)
			if (array.length) return array[0].description
			return ""
		},
		capitalizeFirstLetter(string) {
			return string.charAt(0).toUpperCase() + string.slice(1);
		},
		update(objeto){
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                callback: this.aba.callback
			})
		},
		timestamp(time_str){
			var date = new Date(time_str)
			return date.toLocaleDateString('pt-BR')
		},
		maps(latitude, longitude){
			if (latitude != null && latitude != undefined && longitude != null && longitude != undefined){
				const url = `https://www.google.com/maps/place/${latitude}${longitude}/@${latitude}${longitude}`
				window.open(url, "_blank");
			}
		},
		getStringSlice(request){
			let slice = request.slice(0,30)
			if (slice == request) return request
			else return `${slice} ...`
		},
	}
}
</script>

<style scoped>
.patrimonio-tabela {
  /*margin-top: 10px;*/
  grid-area: patrimonio-tabela;
  background-color: var(--cinza);
}
.tabela{
	color: var(--c3);
	font-size: 0.95em;
	--va-data-table-thead-border: 2px solid var(--c1);
}

.actions-buttons{
	padding-right: 5px;
}

.buttom-space{
	min-height: 220px;
}
</style>