<template>
	<div class="empresa-relatorio" >
		<div class="relatorio-item" v-for="(objeto, index) in objetos" :key="objeto.nome">
			<div @click="e=>showdropdown(e,index)" class="relatorio-titulo">
				<div style="padding:10px;">{{objeto.nome}}</div>
				<div style="padding:5px;"><va-button  size="small"  v-if="show[index] == true" color="verde" text-color="cinza" @click="pdf(objeto.nome)" > Gerar PDF</va-button></div>
			</div>
			<div class="relatorio-dropdown" v-if="show[index] == true"> 
				<span v-if="objeto.nome == 'Inventário de Ativos'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Inventário de Máquinas"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-IM001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="categoria" label="Categoria" color="cinza" :options="getCategoriasAtivos" text-by="description" trackBy='id' valueBy='id' multiple/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo pai" color="cinza" :options="getAtivosSelect" @update-search="searchAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
				</span>
				<span v-if="objeto.nome == 'Relatório de Serviços'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Relatório Mensal de Atividades (RMA)"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-RMA001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="categoria" label="Tipo de Serviço" color="cinza" :options="getCategoriasServicos" text-by="description" trackBy='id' valueBy='id' multiple/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo" color="cinza" :options="getAtivosSelect" @update-search="searchAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-date-input v-model="dtStart" label="Data inicial" color="cinza" /></span>
						<span class="input-width"><va-date-input v-model="dtEnd" label="Data final" color="cinza" /></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width">
							<va-select label="Status" :options="generalStateOptions('Relatório de Serviços')" v-model="generalState" clearable text-by="subtitle" value-by="generalState" track-by="generalState" color="cinza"/>
						</span>
						<span class="input-width">
							<va-select label="Projeto" :options="getProjetos" v-model="project_id" clearable text-by="description" value-by="id" track-by="id" color="cinza" />
						</span>
					</div>
				</span>
				<span v-if="objeto.nome == 'Relatório de Reports'">
					<div class="relatorio-inputs">
						<span class="input-width"><va-input v-model="titulo" label="Título" color="cinza" placeholder="Ex: Relatório de Reports Mensal (RMR)"/></span>
						<span class="input-width"><va-input v-model="numero" label="Número do documento" color="cinza" placeholder="Ex: CME-0033-22-RMR001"/></span>
						
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-select searchable v-model="criticidade" label="Criticidade" color="cinza" :options="getCriticidade" multiple/></span>
						<span class="input-width"><va-select searchable v-model="ativo" label="Ativo" color="cinza" :options="getAtivosSelect" @update-search="searchAtivos" text-by="description" trackBy='id' valueBy='id'/></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width"><va-date-input v-model="dtStart" label="Data inicial" color="cinza" /></span>
						<span class="input-width"><va-date-input v-model="dtEnd" label="Data final" color="cinza" /></span>
					</div>
					<div class="relatorio-inputs">
						<span class="input-width">
							<va-select label="Status" :options="generalStateOptions('Relatório de Reports')" v-model="generalState" clearable text-by="subtitle" value-by="generalState" track-by="generalState" color="cinza" />
						</span>
					</div>
				</span>
			</div>
		</div>
	</div>	
</template>

<script>
//import {url} from '../../configs/global'
import { url_relatorio } from '@/configs/global'
import Servico from '@/models/servico'
import Report from '@/models/report'

export default {
	name: 'EmpresaRelatorios',
	props : ['objetos'],
	data () {
		return {
			show: [false, false ,false],
			titulo: '',
			numero: '',
			categoria: '',
			ativo: '',
			dtStart: new Date(),
			dtEnd: new Date(),
			criticidade: '',
			empresa: '',
			generalState: '',
			searching_ativos: false,
			searching_ativos_value: "",
			project_id: ''
    	}
	},
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		},
		getAtivosSelect(){
			return this.$store.getters['ativos.getSelect']
		},
		getCategoriasAtivos(){
			return this.$store.getters['categorias.get']['ativos']
		},
		getCategoriasServicos(){
			return this.$store.getters['categorias.get']['maintenance']
		},
		getCriticidade(){
			return ['Baixa', 'Média', 'Importante', 'Urgente']
		},
		getProjetos(){
			return this.$store.getters['projetos.get']
		},
	
    },
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
		showdropdown(e,index){
			if(!Array.from(document.querySelectorAll('.relatorio-titulo')).includes(e.target)) return
			if (this.show[index]){
				this.show = [false, false ,false]
			}
			else{
				this.show = [false, false ,false]
				this.show[index] = true
			} 
		}, 
		pdf(tipo){
			let categoria = ""
			if(this.categoria != '' ){
				for (let i = 0; i < this.categoria.length; i++)  categoria = categoria + this.categoria[i].id + ',' ; 
				categoria = categoria.slice(0, -1);
			}
			if(tipo == 'Inventário de Ativos'){
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_ativos?document_number=${this.numero}&title=${this.titulo}&category_id=${categoria}&parent_id=${this.ativo}`
				window.open(url_download, "_blank");
			}
			if(tipo == 'Relatório de Serviços'){
				const dtStart = this.dtStart.getFullYear() + '-' + (this.dtStart.getMonth() + 1)  + '-' + this.dtStart.getDate()
				const dtEnd = this.dtEnd.getFullYear() + '-' + (this.dtEnd.getMonth() + 1)  + '-' + this.dtEnd.getDate() + ' 23:59:59'
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_servicos?document_number=${this.numero}&title=${this.titulo}&maintenance_type_id=${categoria}&ativo_id=${this.ativo}&dtStart=${dtStart}&dtEnd=${dtEnd}&generalState=${this.generalState}&project_id=${this.project_id}`
				window.open(url_download, "_blank");
			}
			if(tipo == 'Relatório de Reports'){
				const dtStart = this.dtStart.getFullYear() + '-' + (this.dtStart.getMonth() + 1)  + '-' + this.dtStart.getDate()
				const dtEnd = this.dtEnd.getFullYear() + '-' + (this.dtEnd.getMonth() + 1)  + '-' + this.dtEnd.getDate() + ' 23:59:59'
				console.log(this.generalState)
				const url_download = `${url_relatorio}/api/${this.empresa.id}/relatorio_reports?document_number=${this.numero}&title=${this.titulo}&criticality=${this.criticidade}&ativo_id=${this.ativo}&dtStart=${dtStart}&dtEnd=${dtEnd}&generalState=${this.generalState}`
				window.open(url_download, "_blank");
			}
			/*this.titulo = ''
			this.numero = ''
			this.categoria = ''
			this.ativo = ''
			this.dtStart = new Date()
			this.dtEnd = new Date()
			this.criticidade = ''
			this.generalState = null*/
		},
		generalStateOptions(tipo){
			//if(this.aba.tipo == 'chamados') return Chamado.DADOS
			if(tipo == 'Relatório de Serviços') return Servico.DADOS
			else if(tipo == 'Relatório de Reports') return Report.DADOS
			//else if(this.aba.tipo == 'inspecoes') return Inspecao.DADOS*/
			return []
		},
		searchAtivos(value){
			if (!value || value.length == 0 ) return
			this.searching_ativos_value = value
			if (this.searching_ativos) return
			this.searching_ativos = true
			setTimeout(()=>{
				this.searching_ativos = false
				this.$store.dispatch("ativos.loadSelect", this.searching_ativos_value);
			},1000)
		},
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.empresa-relatorio {
	margin-bottom: 10px;
	margin-top: 10px;
}
.relatorio-item {
	/*padding: 10px;*/
	margin-bottom: 10px;
	background-color: var(--c1);
	color: var(--branco);
	border-radius: 10px ;
}

/*.relatorio-item:hover{
	background-color: #444753;
	cursor: pointer;
}*/

.relatorio-dropdown{
	padding: 10px;
	width: 70%;
	display: flex;
	flex-direction: column;
	justify-content:space-between;
} 
.relatorio-inputs{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.input-width{
	width: 350px;
	padding: 2px;;
}
.relatorio-titulo{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
		border-radius: 10px ;
}
.relatorio-titulo:hover{
	background-color: #444753;
	cursor: pointer;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #3C3F4A99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container:hover .overlay {
  opacity: 1;
}
</style>