<template>
	<v-chart class="dashboard-chart" :option="getOptions" autoresize @legendselectchanged="legendselectchanged"/>
</template>

<script>
import VChart from "vue-echarts";
//import options from './bar_options'
//import {color} from '@/configs/global'
export default {
	name: 'DashboardChartPie',
	props: ['resume', 'dados', 'color'],
	components : {VChart},
	data() {
		return {
			DADOS: []
		}
	},
	methods: {
		legendselectchanged(){
			console.log('ok')
		},
	},
	computed: {
		getOptions(){
			let op = {
				/*color: options.color,*/
				/*legend: {...options.legend,
				},*/
				color: this.color,
				tooltip: {
					trigger: 'item'
				},
				
				series: {
					name: "",
					type: 'pie',
					radius: ['20%', '60%'],
					emphasis: {
						label: {
						show: true,
						fontSize: '20',
						fontWeight: 'bold',
						color: '#CCCCCC'
						}
					},
					label: {
						show: false,
						position: 'center'
					},
					data: this.dados.map((DADO)=> {
						return {
							name: DADO.subtitle, 
							value: parseInt(DADO.number),
						}
					})
				}
			}
			return op
			
		}
	},
}
</script>

<style scoped>
.dashboard-chart{
	height: 100%;
	/*background-color: red;*/
}
</style>