<template>
	<div class="card-item" @click="navigate">
		<va-icon :name="icon" color="c3" class="ml-2"></va-icon>
		<div class="card-item-number" :style="getColor(color)">{{number}}</div>
		<div class="card-item-subtile">{{subtitle}} </div>
	</div>
</template>

<script>
export default {
	name: 'DashboardCardItems',
	props : ['subtitle','icon', 'number', 'color', 'aba', 'generalState'],
	computed: {
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		},
	},
	methods: {
		getColor(color){
			return 'color:'+color+';'
		},
		navigate(){
			console.log('generalState')
			console.log(this.generalState)
            this.$router.push({path: '/sistema/servicos', params: {loadAll: false}})
			this.$store.dispatch('abas.set', ['/sistema/servicos', this.aba.index])
			let filtros = {
				page: 1,
				generalState: this.generalState,
			}
			const filtroDashboard = this.$store.getters['abas.getFiltro']['dashboard']
			if (filtroDashboard && filtroDashboard['dtStart'] && filtroDashboard['dtEnd']){
				filtros['dtStart'] = filtroDashboard['dtStart']
				filtros['dtEnd']   = filtroDashboard['dtEnd']
				filtros['dtLabel'] = filtroDashboard['dtLabel']
			}
			console.log(filtros, this.aba.tipo)
			setTimeout(()=>{
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, filtros])
			},500)
		},
	},
}
</script>

<style scoped>

.card-item {
	height: 35px;
	background-color: var(--cinza);
	padding: 5px;
	margin: 10px;
	border-radius: 10px;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.card-item:hover{
	cursor: pointer;
	opacity: 0.75;
}
.card-item-number{
	padding: 0px 20px;
	font-size: 1.6em;
	font-weight: bold;
}
.card-item-subtile{
	/* padding-left: 10px; */
	font-size: 1.1em;
	color: var(--c3);
}

</style>