<template>
   	<va-modal v-model="show" :hide-default-actions="true" :mobile-fullscreen="false" max-width="100vw">
		<div class="assinatura-modal">
			<ModalTitle dispatch="assinatura.change_show" :objeto="getObjeto" :title="type == 1 ? 'Assinatura do responsável':'Assinatura do recebedor'"/>
			<div class="assinatura-modal-subtitle"> 
				<div><va-popover message="Voltar para assinatura do responsável">
					<va-button color="c3" icon="arrow_back" flat @click="type = 1" v-if="type ==2"></va-button>
					</va-popover>
				</div>
				<div><va-popover message="Ir para assinatura do recebedor">
					<va-button color="c3" icon="arrow_forward" flat @click="type = 2" v-if="type ==1"></va-button>
					</va-popover>
				</div>
			</div>
			<div class="assinatura-conteudo">
				<div class="assinatura-area">
					<va-input v-model="name" label="Nome do recebedor" color="cinza" v-if="type ==2"></va-input>
					<VueSignaturePad width="400px" height="100px" ref="signaturePad" :customStyle="{backgroundColor: 'white'}" :options="{minWidth: 1, maxWidth: 1}"/>
				</div>
				<div class="assinatura-modal-buttons">
					<span>
					<va-button color="verde" text-color="cinza" class="mt-3 ml-3" @click="save(type)">Enviar</va-button>
					<va-button color="laranja" class="mt-3 ml-3" @click="undo" text-color="cinza">Desfazer</va-button>
					</span>
					<va-button color="c3" flat class="mt-3 ml-3" @click="pular" v-if="type ==2">Pular</va-button>
				</div>
			</div>
		</div>
	</va-modal>
</template>

<script>
export default {
    name: 'AssinaturaModal',
	props: ['modelValue'],
 	emits: ['update:modelValue'],
	data() {
		return {
			type: 1, 
			name: ''
		}
	},
	computed: {
		show: {
			get(){
				return this.$store.getters['assinatura.getShow']
			},
			set(value){
				this.$store.dispatch('assinatura.change_show', value)
			}
		},
		getObjeto(){
			return this.$store.getters['assinatura.getObjeto']
		},
	},
	methods: {
		undo() {
			this.$refs.signaturePad.undoSignature();
		},
		pular(){
			this.type = 1
			this.show = false
		},
		async save(type) {
			try {
				if (this.getObjeto.responsible && this.getObjeto.responsible.member_id){
					if(this.$store.getters['membro.get'].member_id == this.getObjeto.responsible.member_id){
						return this.send(type)
					}
				}
			} catch (error) {
				console.log(error)
			}
			this.$store.dispatch('toast.error', 'Somente o responsável pode assinar a OS!')
		},
		async send(tipo_foto) {
			try {
				const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
				if (isEmpty) return this.$store.dispatch('toast.error', 'Assinatura vazia!')
				this.$store.dispatch('loading.show')
				const blob = await (await fetch(data)).blob()
				await this.getObjeto.savePhoto({
					getters: this.$store.getters,
					blob,
					type: tipo_foto,
					description: this.name
				})
				this.$store.dispatch('toast.success', 'Enviado!')
				this.type = this.type + 1
				if(this.type == 3){
					this.type = 1
					this.show = false
				}
				else{
					this.$refs.signaturePad.clearSignature()
				}
				//this.show = false
			} catch (error) {
				console.log(error)
				this.$store.dispatch('toast.error', 'Erro na assinatura!')
			}
			this.$store.dispatch('loading.hide')
		}
 	 }
}
</script>

<style scoped>
.assinatura-modal{
	display: flex;
	flex-direction: column;
	/*align-items: center;*/
	color: var(--branco);
	min-width: 50vw;
    min-height: 75vh;
}
.assinatura-modal-subtitle{
	color: var(--c3);
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.assinatura-modal-buttons{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.assinatura-area{
	width: 400px;
}
.assinatura-conteudo{
	display: flex;
	flex-direction: column;
	/*justify-content: space-between;*/
	align-items: center;
}

</style>