<template>
    <div class="system-menu-img" >
        <img v-if="getEmpresa.url_image" :src="getEmpresa.getImageURL()" alt="" class="system-menu-img-content" >
        <EmpresaImageDefault v-else class="system-menu-img"/>
        <div >
            <div class="system-menu-img-text"> {{getEmpresa.company_name}} </div>
            <div class="system-menu-img-cnpj" v-if="getEmpresa.cnpj != 'null' && getEmpresa.cnpj != undefined && getEmpresa.cnpj != '' && getEmpresa.cnpj != null ">{{getEmpresa.cnpj}}</div>
        </div>
    </div>
</template>

<script>
import EmpresaImageDefault from '../../utils/EmpresaImageDefault'
export default {
    name: 'SystemMenuImage',
    components: {EmpresaImageDefault},
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		}
    }
}
</script>

<style scoped>
.system-menu-img{
    width: 100%;
    min-height: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
}

.system-menu-img-text{
    color: var(--c3);
    padding: 0 0 0 10px;
    font-weight: bold;
}
.system-menu-img-cnpj{
    color: #CCCCCC90;
    font-size: 0.8em;
    padding: 7px 0 0 10px;
}

.system-menu-img-content{
    width: 50px;
    height: 50px !important;
    border-radius: 5px;
}


@media only screen and (max-width: 600px) {
    .system-menu-img{
        display: none;
    }
}
</style>