<template>
	<FloatButton @click="add()" icon="add" type="vuestic" style="margin-bottom: 70px;"/>
	<FloatButton v-if="aba.tipo == 'servicos'" @click="openCalendar" icon="calendar_month" type="vuestic" style="bottom: 160px" />
	<!--<span v-if="aba.tipo != 'relatorios'">
		<FloatButton @click="add()" icon="add" type="vuestic" style="margin-bottom: 70px;"/>
		<FloatButton @click="showAll = !showAll" :icon="showAll? 'visibility_off' : 'visibility'" type="vuestic" style="margin-bottom: 140px;"/>
	</span>-->
	<Estatistica v-if="aba.tipo == 'reports' && 0" :objetos="getObjetos" :aba="aba" img="reports.svg" caption='"Se você não pode tocar, cadastre aqui."' />
    <Estatistica v-if="aba.tipo == 'servicos' && 0" :objetos="getObjetos" :aba="aba" img="" caption="" />
	<ModalCalendar :objetos="getObjetos" :aba="aba"> </ModalCalendar>
	<ModalServicoConfig :aba="aba" />
	<ModalInspecoes/>
	<!-- <ModalChamados/> -->
	<ModalReportsFinish/>
    <TabelaGeral :aba="aba" :objetos="getObjetos"/>
</template>

<script>
import FloatButton from '@/utils/FloatButton'
import Estatistica from "@/utils/Estatistica";
import ModalCalendar from "@/components/Servicos/ModalCalendar";
import ModalServicoConfig from "@/components/Servicos/ModalServicoConfig";
import ModalInspecoes from "@/components/Servicos/ModalInspecoes";
// import ModalChamados from "@/components/Servicos/ModalChamados";
import ModalReportsFinish from "@/components/Servicos/ModalReportsFinish";
import TabelaGeral from '../utils/TabelaGeral'
export default {
    name: 'SystemServicos',
	data() {
		return {
			showAll: false
		}
	},
	components:{FloatButton, TabelaGeral, Estatistica, ModalCalendar, ModalServicoConfig, ModalInspecoes, ModalReportsFinish},/*, ModalChamados },*/
	computed: {
		getObjetos(){
			//if (this.aba.tipo == "ativos") return this.$store.getters["ativos.get"];
			if (this.aba.tipo == "reports") return this.$store.getters["reports.get"];
			if (this.aba.tipo == "servicos") return this.$store.getters["servicos.get"];
			if (this.aba.tipo == "inspecoes") return this.$store.getters["inspecoes.get"];
			if (this.aba.tipo == "chamados") return this.$store.getters["chamados.get"];
			else return []
			//else return  this.$store.getters[this.aba.getters]
		},
		aba(){
			const index = this.$store.getters['abas.getIndices'][this.$route.path]
			const abas = this.$store.getters['abas.getAbas'][this.$route.path]
			return index != undefined ? abas[index] : {}
		},
		id_empresa(){
			return this.$store.getters['empresa.get'].id
		},
	},
	methods:{
		add(){
			let permissao =  this.$store.getters['membro.getPermissao'];
			if(permissao < 100 || this.aba.model == 'chamado'){
				this.$store.dispatch('edit-modal.editShow', {
					objeto: this.aba.model,
					callback: this.aba.callback
				})
			}
			else{
				this.$store.dispatch('toast.error', 'Não autorizado!')
			}
		},
		openCalendar() {
			this.$store.dispatch("servicos.calendar");
		},
	}
}
</script>

