import { url, } from '@/configs/global'
import model from './__model__'

class Categoria extends model{

    static bases = [{'id': 'ativos', 'label': 'Ativos'}, {'id': 'serviços', 'label': 'Serviços'}, ]

    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'categoria'

        this.listaAtualizacao = [
            {label: 'description', type: 'text', labelInput: 'Descrição'},
        ]
        this.listaCriacao = [
            ...this.listaAtualizacao,
            {label: 'base', type: 'select', textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Base',
                options: () => Categoria.bases},
                {label: 'public', type: 'select', textBy: 'label', valueBy: 'id', trackBy: 'id', labelInput: 'Privacidade',
                    options: () => [{'id': false, 'label': 'Privado'}, {'id': true, 'label': 'Público'}, ]},
            {label: 'segment_id', type: 'select', textBy: 'description', valueBy: 'id', trackBy: 'id', labelInput: 'Segmento',
                options: ({getters}) => getters['admin.getSegmentos']},
        ]
    }

    rota(){
        if (this.base == 'serviços'){
            return 'service_maintenance_type'
        }else{
            return 'category'
        }
    }

    async create(){
        const obj = this.filterCreate()
        return await this.axios.post(`${url}/api/save_${this.rota()}`, obj)
    }

    async update(){
        const obj = this.filterUpdate()
        return await this.axios.put(`${url}/api/update_${this.rota()}/${this.id}`, obj)
    }
}

export default Categoria