<template>
    <va-modal max-width="100vw" v-model="show" :mobile-fullscreen="false" :hide-default-actions="true" bordered>
    <va-inner-loading :loading="loading" class="dispositivos-chat">
		<ModalTitle dispatch="comentarios.change" :objeto="getObjeto" :title="getTitle" />
        <span>
        <div class="dispositivos-chat-conteudo">
            <ComentariosMsg v-for="note in notes" :key="note.id"  :note="note" :objeto="getObjeto"/>
        </div>
        <div class="dispositivos-chat-msg">
            <va-input color="cinza" type="textarea" :min-rows="3" :max-rows="5" style="padding: 10px;" class="dispositivos-chat-input"  v-model="MENSAGEM" />  
            <va-button color="verde" icon="send" class="dispositivos-chat-button" @click="sendMsg"></va-button>
        </div>
        </span>
    </va-inner-loading>
    </va-modal>
</template>

<script>
import ChamadoComentario from '../../models/chamado_comentario';
import ReportComentario from '../../models/report_comentario';
import ServicoComentario from '../../models/servico_comentario';
import ComentariosMsg from './ComentariosMsg'
export default {
    name: 'Comentarios',
    data: () => ({
        MENSAGEM: null,
        loading: false,
    }),
    components: {ComentariosMsg, },
    computed: {
        show: {
            get(){
                console.log('ok')
			    return this.$store.getters['comentarios.getShow'];
            },
            set(value){
                this.$store.dispatch('comentarios.change',value);
            }
        },
		getObjeto(){
            return this.$store.getters['comentarios.getObjeto'];
		},
        getTitle(){
            return `Comentários do ${this.getObjeto.model_type} ${this.getObjeto.id}`
        },
		notes(){
            let objeto = this.$store.getters['comentarios.getObjeto'];
            let notes = objeto.notes;
            for (let i = 0; i < notes.length; i++) {
                if (notes[i].created_at){
                    notes[i].created_date = new Date(Date.parse(notes[i].created_at))
                }
            }
            notes = notes.sort((a,b) => a.created_date < b.created_date ? 1 : -1)
            return notes
		},
    },
    methods: {
        async sendMsg(){
            this.loading = true
            console.log(this.getObjeto)
            try {
                if (this.getObjeto.model_type == 'chamado'){
                    const new_obj = new ChamadoComentario({
                        called_id: this.getObjeto.id,
                        description: this.MENSAGEM
                    }, this.axios)
                    await new_obj.create({
                        getters: this.$store.getters
                    })
                }
                else if (this.getObjeto.model_type == 'report'){
                     const new_obj = new ReportComentario({
                        report_id: this.getObjeto.id,
                        description: this.MENSAGEM
                    }, this.axios)
                    await new_obj.create({
                        getters: this.$store.getters
                    })
                }
                else if (this.getObjeto.model_type == 'servico'){
                     const new_obj = new ServicoComentario({
                        service_id: this.getObjeto.id,
                        description: this.MENSAGEM
                    }, this.axios)
                    await new_obj.create({
                        getters: this.$store.getters
                    })
                }
               
                await this.getObjeto.getNotes({ getters: this.$store.getters })
                this.$store.dispatch('toast.success', 'Enviado!')
            } catch (error) {
                this.$store.dispatch('toast.error',error)
            }
            this.MENSAGEM = ""
            this.loading = null
        }
    }
}
</script>

<style scoped>
.dispositivos-chat{
	min-width: 50vw;
    min-height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.dispositivos-chat-conteudo{
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-start;
    min-height: 350px;
    min-width:40vw;
    overflow-y: auto;
    /*background-color: blue;*/
}
.dispositivos-chat-msg{
    width: 100%;
    display: flex;
    flex-direction: row;
}
.dispositivos-chat-input{
    width: 100%;
}
.dispositivos-chat-button{
    margin-top: 20px;
}


</style>