<template>
<div class="system-topbar">
    <div class="system-topbar-empresa"> 
      <div>{{aba.nome}}</div> 
      <div style="font-size: 0.8em; font-weight: normal;" v-if="aba.description"> {{aba.description}}</div> 
    </div>
    
    <div id="system-topbar-rightside" style="display:flex; align-items: center; max-width: 650px">
      <SystemTopbarFiltro v-if="['/sistema/dashboard'].includes($route.path)" color="cinza"/>
      <SystemTopbarUser />
    </div>
</div>
</template>

<script>
import SystemTopbarUser from '@/components/System/SystemTopbarUser';
import SystemTopbarFiltro from './SystemTopbarFiltro.vue'
export default {
    name: 'SystemTitle',
    components: {SystemTopbarUser, SystemTopbarFiltro},
    computed: {
        getAbas(){
            return this.$store.getters['abas.getAbas'][this.$route.path]
        },
        aba() {
            const index = this.$store.getters["abas.getIndices"][this.$route.path];
            const abas = this.$store.getters["abas.getAbas"][this.$route.path];
            return index != undefined && abas != undefined ? abas[index] : {};
        },
    },

}
</script>

<style scoped>
.system-topbar{
    grid-area: system-topbar;
    background-color: var(--cinza);
    display: flex;
    justify-content:space-between;
    align-items: center;
    width: 100%;
}
.system-topbar-empresa{
    margin-left: 25px;
    color: var(--c3);
    font-weight: bold;
}
.system-topbar-filter{
	display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 600px) {
  .system-topbar{
    width: 100vw;
  }
  .system-topbar-empresa {
    display: none;
  }
}

</style>