<template>
    <div class="system-menu-button" @click="aba_atual=route"  :style="$route.path == route ? 'background-color: rgba(0, 0, 0, 0.1);' : ''">
        <fa      v-if="icon != undefined && (type == 'fa'||type == undefined)" :icon="icon" class="system-menu-button-icon"/>
        <va-icon v-if="icon != undefined && type == 'vuestic'" :name="icon" class="system-menu-button-icon" size="1.2rem" />
        <span class="system-menu-button-title">{{title}}</span>
    </div>
    <div v-for="(aba,index) in getAbas" :key="index" >
        <div v-if="aba_atual==route" class="system-menu-button-child"  @click="select_aba(index)">
            {{aba.nome}}
            <div style="margin-right: -20px;" v-if="this.aba == aba">
                <va-icon name="signal_cellular_4_bar" color="cinza" size="large" rotation="135" ></va-icon>
            </div>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'SystemMenuButton',
    props:{
        title: String,
        icon: String,
        route: String,
        type: String,
    },
    computed:{
        aba_atual: {
            get(){
                return this.$store.getters['abas.getAtual']
            },
            set(value){
                this.$store.dispatch('abas.setAtual', value)
            }
        },
        getEmpresa(){
            return this.$store.getters['empresa.get'].ID
        },
        getAbas(){
            return this.$store.getters['abas.getAbas'][this.route]
        },
        vIF(){
            return this.getAbas != null && this.getAbas != undefined && this.getAbas.length > 0
        },
        aba() {
            const index = this.$store.getters["abas.getIndices"][this.$route.path];
            const abas = this.$store.getters["abas.getAbas"][this.$route.path];
            return index != undefined && abas != undefined ? abas[index] : {};
        },
    },
    
    methods: {
        select_aba(index){
            if (this.vIF){
                this.$router.push({path: this.route})
                this.$store.dispatch('abas.set', [this.route, index])
            }
		},
    },
    mounted() {
        if (this.$route.path == this.route){
            this.aba_atual = this.$route.path
        }
    },
}
</script>

<style>
.system-menu-button{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    /*justify-content: center;*/
    color: var(--c3);
    padding: 10px 10px;
    /*border-bottom: 0.5px solid var(--cinza);
    border-top: 0.5px solid var(--cinza);*/
    /*background-color: rgba(0, 0, 0, 0.1);*/
}
.system-menu-button:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.1);
}
.system-menu-button-title{
    font-size: 1rem;
    /*margin-top: 10px;*/
}
.system-menu-button-icon{
    font-size: 1rem;
    padding: 0 5px 0  0 ;
}

.system-menu-button-child{
    width: 220px;
    height: 30px;
    color: var(--c3);
    padding: 0px 0px 0px 35px ;
    align-items: center;
    font-size: 0.9rem;
    background-color: var(--c1);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.system-menu-button-child:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 600px) {
    .system-menu-button{
        padding: 2px 2px;
    }
    .system-menu-button-title{
        font-size: 0.5rem;
    }
    .system-menu-button-icon{
        font-size: 1.07rem;
    }
}
</style>