<template>
    <va-modal v-model="show" :hide-default-actions="true" :mobile-fullscreen="false" max-width="100vw">
	<div class="modal-documentos">
		<ModalTitle dispatch="documentos.change_show" :objeto="getParente" :title="aba.tipo == 'planos'? 'LISTA DE SUBPLANOS' : 'LISTA DE SEÇÕES'"/>
		<div class="modal-documentos-header">
			<va-button color="verde" class="mb-3" icon="add" text-color="cinza"
				@click="update( aba.tipo == 'planos' ?  'plano_frequencia' : 'checklist_section', getParente)"
					size="small">Adicionar {{aba.tipo == 'planos'? 'subplano' : 'seção'}}</va-button>
		</div>
		<div v-for="objeto in objetos" :key="objeto.id" class="modal-documentos-objeto">
			<div class="objeto-titulo">
				<div @click="objeto.dropdown_ativado = !objeto.dropdown_ativado">
					{{objeto.description}}
				</div> 
				<span>
					<va-button flat class="mr-1" color="verde"  icon="add"
						@click="update(aba.tipo == 'planos' ?  'plano_frequencia_item' : 'checklist_section_item', objeto)" size="small" />
					<va-button flat color="verde" icon="edit" @click="update(objeto)" size="small" />
				</span>
			</div>

			<div v-if="objeto.dropdown_ativado" class="objeto-conteudo">
				<div v-if="this.aba.tipo == 'planos'">
					<div v-for="item in objeto.items" :key="item.id" class="sub-itens" >
						{{item.task_description}}
						<div style="width: 30%; display: flex; justify-content: space-between; ">
							<span><va-icon name='update' ></va-icon> {{item.frequency_day}} dia(s) </span>
							HH: {{item.hh}}
							<va-button flat color="verde" icon="edit" @click="update(item)" size="small" />
						</div> 
					</div>
				</div>
				<div v-if="this.aba.tipo == 'checklists'">
					<div v-for="item in objeto.items" :key="item.id" class="sub-itens" >
						{{item.description}}
						<div style="width: 30%; display: flex; justify-content: space-between; ">
							Peso: {{item.weight}}
							<div>
								<!--<va-button flat color="verde" icon="visibility" @click="update(item)" size="small" />-->
								<va-button flat color="verde" icon="edit" @click="update(item)" size="small" />
							</div>
						</div> 

					</div>
				</div>
			</div>
		</div>
	</div>
    </va-modal>
</template>

<script>
export default {
	name: "ModalDocumentos",
	methods: {
		update(objeto, parente={}){
			let permissao =  this.$store.getters['membro.getPermissao'];
			if(permissao < 2 || (permissao < 100 && this.getParente.public == 0)){
				this.$store.dispatch('edit-modal.editShow', {
					objeto: objeto,
					callback: this.aba.callback,
					id_parente: parente.id
				})
			}
			else{
				this.$store.dispatch('toast.error', 'Não autorizado!')
			}
		},
	},
	computed: {
		show: {
			get(){
				return this.$store.getters['documentos.getShow']
			},
			set(show){
				this.$store.dispatch('documentos.change_show', show)
			}
		},
		aba() {
			return this.$store.getters['documentos.getAba']
		},
		objetos() {
			return this.$store.getters['documentos.getObjetos']
		},
		getParente(){
			return this.$store.getters['documentos.getParente']
		}
	},
}
</script>

<style scoped>
.modal-documentos{
	color: white;
	min-width: 50vw;
    min-height: 75vh;
	display: flex;
	flex-direction: column;
}
.modal-documentos-header{
	display: flex;
	justify-content: space-between;
}
.modal-documentos-objeto{
	margin-bottom: 10px;
}
.objeto-titulo{
	background-color: var(--c4);
	padding: 0 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.objeto-titulo div {
	width:90%;
	padding: 10px 5px;
}
.objeto-titulo div:hover{
	cursor: pointer;
}
.sub-itens{
	background-color: var(--c1);
	/*margin-left: 20px;*/
	/*margin-top: 2px;*/
	/*padding: 5px;*/
	padding: 10px 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.objeto-conteudo{
	padding: 0px;
}
</style>