<template>
   	<va-modal v-model="editShow" :hide-default-actions="true" :mobile-fullscreen="false" max-width="100vw">
	<div class="edit-modal">
		<ModalTitle :dispatch="`${store_key}.change_editShow`" :objeto="objeto" :hidebuttons="!isEdit" :title="`${isEdit? 'Editar' : 'Adicionar'} ${objeto.model_type}`" />
		<span v-for="value in getLista" :key="value.label">
			<span v-if="value.vIF == undefined || value.vIF(objeto)">
			<div v-if="typeof(value)=='string'" class="divisao-titulo">
				{{value}}
			</div>
			<!-- Select -------------- -->
			<va-select v-else-if="value.type == 'select'" searchable clearable color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]" :options="getOptions(value)" :text-by="value.textBy" :value-by="value.valueBy" :track-by="value.trackBy" @update:model-value="value.change!=undefined ? value.change(getOptions(value)) : ()=>{}"  :disabled="value.readonly" @update-search="v => searchFunction(v, value.searchCallback)"></va-select>	
			<!-- Colorinput ---------- -->
			<span v-else-if="value.type == 'colorinput'" style="display: flex; justify-content: end;">
				<input type="color" :value="objeto[value.label]" @change="(event) => objeto[value.label] = event.target.value" :config="{enableTime: true}">
			</span>	
			<!-- Option List---------- -->
			<span v-else-if="value.type == 'option-list' && getOptions(value).length > 0">
				<va-option-list color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]" :options="getOptions(value)" :text-by="value.textBy" :value-by="value.valueBy" :track-by="value.trackBy"/>
			</span>
			<!-- Date input----------- -->
			<span v-else-if="value.type == 'date-input'" class="date-input-span" >
				<va-date-input color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]"/>
				<va-time-input v-if="value.time" v-model="objeto[value.label + '-time']"/>
			</span>
			<!-- Date - time---------- -->
			<span v-else-if="value.type == 'datetime'">
				<va-datetime type="string" :onlyDate="value.onlyDate" color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]"/>
			</span>
			<!-- Number     ---------- -->
			<va-input v-else-if="value.type == 'number'" :type="value.inputType ? value.inputType : 'number'" color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]" :readonly="value.readonly">
				<template v-if="value.append" #append>
					<va-button color="verde" @click="objeto.onEnter(value)" class="ml-3">Enviar</va-button>
				</template>
			</va-input>	
			<!-- Text ---------------- -->
			<va-input v-else-if="value.type == 'text'" :type="value.inputType ? value.inputType : 'text'" autosize color="secondary" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" v-model="objeto[value.label]" :readonly="value.readonly">
				<template v-if="value.append" #append>
					<va-button color="verde" @click="objeto.onEnter(value)" class="ml-3">Enviar</va-button>
				</template>
			</va-input>	
			<!-- Switch -------------- -->
			<va-switch v-else-if="value.type == 'switch' && getPermissaoMembro < value.permission"  color="verde" class="mb-3" :label="value.labelInput ? value.labelInput : value.label" left-label v-model="objeto[value.label]" size="small" @update:model-value="confirmarModelo()">
			</va-switch>
			<!-- button finalizar/aprovar/cancelar -------------- -->
			<div v-else-if="value.type == 'buttom' && getPermissaoMembro < value.permission"  class="mb-3">
				{{value.labelInput}}
				<va-button size="small" color="azul" class="ml-3"  @click="aprovarFinalizar(value.label, true)" text-color="cinza">Enviar</va-button>
				<va-button v-if="value.label == 'approved'" size="small" color="azul" class="ml-3"  @click="aprovarFinalizar(value.label, false)" text-color="cinza">Reprovar</va-button>
			</div> 
		</span>
		</span>
		<va-button v-if="objeto.update != undefined || !isEdit" color="verde" text-color="cinza" class="mt-3" @click="confirm" :disabled="click? true : false">{{isEdit? 'Atualizar' : 'Criar'}}</va-button>
		<va-button v-if="isEdit && objeto.allowRemove" color="laranja" class="mt-3" @click="remove" text-color="cinza">Remover</va-button>
	</div>
	</va-modal>
</template>

<script>
import VaDatetime from '@/utils/VaDatetime'
export default {
    name: 'EditModal',
	components:{VaDatetime},
	data: () => ({
        editShow: false,
		objeto: {},
		isEdit: false,
		store_key: 'edit-modal',
		click: false,
    }),
	computed: {
		getLista(){
			let lista = this.isEdit ? this.objeto.listaAtualizacao : this.objeto.listaCriacao
			return lista
		},
		getEditShow(){
			return this.$store.getters[`${this.store_key}.getEditShow`];
		},
		getEditObjeto(){
			return this.$store.getters[`${this.store_key}.getEditObjeto`];
		},
		getAdmin(){
			return this.$store.getters['usuario.get'].ADMIN
		},
		aba() {
			const index = this.$store.getters["abas.getIndices"][this.$route.path];
			const abas = this.$store.getters["abas.getAbas"][this.$route.path];
			return index != undefined && abas ? abas[index] : {};
		},
		getPermissaoMembro(){
			return this.$store.getters['membro.getPermissao']
		},
		getUsuario(){
			return this.$store.getters['usuario.get']
		},
		getMembro(){
			return this.$store.getters['membro.get']
		}
	},
	methods:{
		getOptions(value){
			try {
				const res = value.options({getters: this.$store.getters})
				if (res == null || res == undefined) throw 'error'
				return res
			} catch (error) {
				return []
			}
		},
		// ############### Função criação de modelo de report #############################################
		confirmarModelo(){
			if(this.objeto['is_model'] == true){
				if(confirm("Tem certeza que deseja transformar esse report em Modelo?") == false ){
					this.objeto['is_model'] = false
				}
			}
		},
		// ############### Confirmar edição ou criaçãot ###################################################
		
		getPermissoes(del=false){
			console.log(this.objeto)
			if(( ['chamado'].includes(this.aba.model) && this.isEdit && this.objeto.created_by != this.getUsuario.id )) return false //apenas quem criou o chamado por editá-lo
			else if(['plano', 'checklist'].includes(this.aba.model) && (this.objeto.public == 1 && this.getPermissaoMembro >= 2)) return false  //membros não podem criar/editar planos e checklists publicos
			else if(['servico', 'report', 'inspecao', 'ativo','plano', 'checklist'].includes(this.aba.model) && this.getPermissaoMembro >= 100) return false // convidados não podem criar/editar nada exceto chamados
			else if(['servico','inspecao'].includes(this.aba.model) && this.getPermissaoMembro >= 2 && del == true) return false // membros não podem deletar inspeções e serviços
			else if(['report'].includes(this.aba.model) && this.getPermissaoMembro >= 2 && del == true && this.objeto.created_by != this.getUsuario.id) return false // membros não podem deletar reports que não foram criados por eles
			else if(['mantenedor', 'membros'].includes(this.aba.model) && this.getPermissaoMembro >= 2 ) return false // membros não podem criar a editar mantenedores
			else if(['servico'].includes(this.aba.model) && this.getPermissaoMembro >= 1 && (this.objeto.finished_approved == 1 || this.objeto.finish == 1 )) return false // não é possível alterar serviços finalizados
			else return true
		},
		async confirm(){
			console.log(this.objeto)
			this.click = true
			try {
				if(this.getPermissoes()){
					this.$store.dispatch('loading.show')
					if (this.isEdit){
						/*this.$store.dispatch('toast.error', 'Sistema em manutenção!');
						this.$store.dispatch('loading.hide')
						return*/
						await this.objeto.update({
							getters: this.$store.getters
						})
					}else{
						await this.objeto.create({
							getters: this.$store.getters
						})
					}
					this.$store.dispatch('toast.success', 'Atualizado');
					try {
						const callback = this.$store.getters['edit-modal.getCallback']
						await this.$store.dispatch(callback, this.aba.tipo)
						await this.refreshObject()
						
					} catch (error) {
						console.log(error)
					}
				}
				else{
					this.$store.dispatch('toast.error', 'Não autorizado!')
				}
				this.editShow = false
			} catch (err) {
				this.$store.dispatch('toast.error', err);
			}	
			this.click = false
			this.$store.dispatch('loading.hide')
		},
		// ############### Aprovar, finalizar e cancelar Serviços, Reports e Chamados ############################
		async aprovarFinalizar(label, bool){
			try {
				if(this.aba.tipo == 'servicos' || this.aba.tipo == 'reports'){
					if(label == 'finish'){
						// apenas admin e PCM podem finalizar serviços ou o responsável pelo serviço
						console.log(this.getUsuario)
						console.log(this.objeto.responsible_id)
						if(this.getPermissaoMembro < 2 || this.objeto.responsible.member_id ==  this.getMembro.member_id){ 
							this.$store.dispatch('loading.show')
							await this.objeto.finishServiceById({getters: this.$store.getters})
							this.$store.dispatch('toast.success', 'Execução do serviço finalizada');
							try {
								const callback = this.$store.getters['edit-modal.getCallback']
								await this.$store.dispatch(callback, this.aba.tipo)
								await this.refreshObject()
							} catch (error) {
								console.log(error)
							}
						}
						else{
							this.$store.dispatch('toast.error', 'Não autorizado! Apenas o responsável pelo serviço ou administradores podem finalizar execução.')
						}
					}
					else if (label == 'finished_approved'){
						if(this.getPermissaoMembro < 2){ // apenas admin e PCM podem aprovar serviços
							this.$store.dispatch('loading.show')
							await this.objeto.approveFinish({ getters: this.$store.getters})
							this.$store.dispatch('toast.success', 'Serviço aprovado e concluído.');
							try {
								const callback = this.$store.getters['edit-modal.getCallback']
								await this.$store.dispatch(callback, this.aba.tipo)
								await this.refreshObject()
							} catch (error) {
								console.log(error)
							}
						}
						else{
							this.$store.dispatch('toast.error', 'Não autorizado!')
						}
					}
					else if(label == 'canceled'){
						if(confirm("Tem certeza que deseja enviar cancelamento?") == true){
							this.objeto['canceled'] = true
							this.confirm()
							this.$store.dispatch('toast.success', 'Cancelamento enviado.')
						}
					}
					else if(label == 'undo_finish'){
						if(confirm("Tem certeza que deseja reabrir este serviço?") == true){
							this.objeto['finish'] = false
							this.confirm()
							this.$store.dispatch('toast.success', 'OS reaberta com sucesso.')
						}
					}

				}
				else if(this.aba.tipo == 'chamados'){
					if(label == 'finished') this.objeto['finished'] = true
					else if (label == 'approved' && !bool) this.objeto['approved'] = false
					else if (label == 'approved') this.objeto['approved'] = true
					if(this.getPermissaoMembro < 2 ){ // apenas admin e PCM podem finalizar e aprovar chamados pelo serviço
						this.$store.dispatch('loading.show')
						await this.objeto.approve({getters: this.$store.getters})
						if(label == 'finished') this.$store.dispatch('toast.success', 'Chamado finalizado.')
						else if (label == 'approved' && !bool) this.$store.dispatch('toast.success', 'Chamado reprovado.')
						else if (label == 'approved') this.$store.dispatch('toast.success', 'Chamado aprovado.')
						try {
							const callback = this.$store.getters['edit-modal.getCallback']
							await this.$store.dispatch(callback, this.aba.tipo)
							await this.refreshObject()
						} catch (error) {
							console.log(error)
						}
					}
					else{
						this.$store.dispatch('toast.error', 'Não autorizado!')
					}
				}

			} catch (err) {
				this.$store.dispatch('toast.error', err);
				await this.$store.dispatch(this.$store.getters['edit-modal.getCallback'], this.aba.tipo)
				this.editShow = false
			}	
			this.$store.dispatch('loading.hide')
		},
		
		async remove(){
				if (confirm("Tem certeza que deseja remover?") == true) {
					if(this.getPermissoes(true)){
						try {
							this.$store.dispatch('loading.show')
							await this.objeto.delete({
								getters: this.$store.getters
							})
							this.$store.dispatch('toast.success', 'Removido');
						} catch (err) {
							this.$store.dispatch('toast.error', err);
						}	
						try {
							const callback = this.$store.getters['edit-modal.getCallback']
							await this.$store.dispatch(callback, this.aba.tipo)
							await this.refreshObject()
						} catch (error) {
							console.log(error)
						}
						this.editShow = false
						this.$store.dispatch('loading.hide')
					}
				else{
					this.$store.dispatch('toast.error', 'Não autorizado!')
				}
			}
		},

		async send_config(msg){
			await this.objeto.send_config(msg)
		},

		async refreshObject(){
			const action = this.$store.getters['edit-modal.getAction']
			if(action && action.getters && action.dispatch && action.objeto.id){
				const filter = this.$store.getters[action.getters].filter(e => e.id == action.objeto.id)
				if (filter.length == 1 && filter[0].type == action.objeto.type){
					await filter[0].loadSubtasks({getters: this.$store.getters})
					this.$store.dispatch(action.dispatch, filter[0])
				}
			}

		},

		searchFunction(valor, callback){
			if (callback && typeof(callback)=='function' && valor){
				callback({
					dispatch: this.$store.dispatch,
					valor: valor
				})
			}
		}
		
	},
    watch: {
        editShow: function (value) {
            this.$store.dispatch(`${this.store_key}.change_editShow`, value);
        },
        getEditShow: function(value){
            this.editShow = value
        },
        getEditObjeto: function(value){
			this.objeto = value
			if (value.id != undefined){
				this.isEdit = true
			}else{
				this.isEdit = false
			}
        }
    }
}
</script>

<style scoped>
.edit-modal{
	display: flex;
	flex-direction: column;
	color: var(--branco);
	min-width: 50vw;
    min-height: 75vh;
}
.edit-modal-titulo{
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 25px;
	text-transform: uppercase; 
	color: var(--verde);
}
.date-input-span{
	display: flex;
	flex-direction: row;
	
}
.divisao-titulo{
	border-bottom: 1px solid white;
	margin-bottom: 10px;
	font-size: 1.0rem;
	text-transform: uppercase;
}

</style>