<template>
   <div class="tabela-geral-filtro">
	    <div class="filtros">
      		<SystemTopbarFiltro v-if="['/sistema/servicos'].includes($route.path)" color="verde"/>
			<va-input v-if="['reports', 'ativos', 'servicos', 'admin-usuarios', 'admin-empresas', 'admin-categorias', 'checklists', 'planos'].includes(aba.tipo)" label="Descrição" v-model="filtroDescription" class="ml-1" id="inputfiltro" color="verde" clearable/>
			<va-select v-if="['reports', 'ativos'].includes(aba.tipo)" :label="aba.tipo == 'ativos' ? 'Ativo Parente' : 'Ativo Relacionado' " :options="getAtivos" v-model="filtroAtivo" clearable :text-by="(e) => `${e.description} (${e.patrimony})`" value-by="id" track-by="id" color="verde" :searchable="aba.tipo=='reports'" @update-search="searchAtivos" class="ml-1"/>
			<va-select v-if="['admin-categorias'].includes(aba.tipo)" label="Base" :options="['ativos','serviços']" v-model="filtroBase" clearable color="verde" class="ml-1"/>
			<va-select v-if="['admin-categorias'].includes(aba.tipo)" label="Segmento" :options="getSegmentos" v-model="filtroSegmento" clearable text-by="description" value-by="id" track-by="id" color="verde" class="ml-1"/>
			<va-select v-if="['checklists', 'planos'].includes(aba.tipo)" label="Privacidade" :options="[{'id': false, 'label': 'Privado'}, {'id': true, 'label': 'Público'}, ]" v-model="filtroPrivacidade" clearable text-by="label" value-by="id" track-by="id" color="verde" class="ml-1"/>
			<va-select v-if="['reports'].includes(aba.tipo)" label="Tipo" :options="[{'id': false, 'label': 'Comum'}, {'id': true, 'label': 'Modelo'}, ]" v-model="filtroModelo" clearable text-by="label" value-by="id" track-by="id" color="verde" class="ml-1"/>
			<va-select v-if="['servicos'].includes(aba.tipo)" label="Responsável" :options="getMantenedores" v-model="filtroResponsavel" clearable text-by="name" value-by="id" track-by="id" color="verde" class="ml-1"/>
			<va-select v-if="['chamados'].includes(aba.tipo)" label="Atribuído" :options="getMembros" v-model="filtroMembro" clearable text-by="name" value-by="id" track-by="id" color="verde" class="ml-1"/>
			<va-select v-if="['reports', 'chamados','servicos','inspecoes'].includes(aba.tipo)" label="Estado" :options="generalStateOptions" v-model="filtroGeneralState" clearable text-by="subtitle" value-by="generalState" track-by="generalState" color="verde" class="ml-1"/>
		</div>
		<va-pagination v-model="currentPage" :pages="lastPage" size="small" color="#3CDE93" hide-on-single-page flat :visible-pages="1"/>
   </div>
</template>

<script>
import Chamado from '../models/chamado'
import Servico from '../models/servico'
import Report from '../models/report'
import Inspecao from '../models/inspecao'
import SystemTopbarFiltro from '../components/System/SystemTopbarFiltro.vue'
export default {
    name: 'TabelaGeralFiltro',
	components: {SystemTopbarFiltro, },
	data() {
		return {
			limpar: false,
			searching_ativos: false,
			searching_ativos_value: ""
		}
	},
	props : ['aba'],
	computed: {
		generalStateOptions(){
			if(this.aba.tipo == 'chamados') return Chamado.DADOS
			else if(this.aba.tipo == 'servicos') return Servico.DADOS
			else if(this.aba.tipo == 'reports') return Report.DADOS
			else if(this.aba.tipo == 'inspecoes') return Inspecao.DADOS
			return []
		},
		getAtivos(){
			if (this.aba.tipo == 'ativos') return this.$store.getters['ativos.getWithChildren']
			return this.$store.getters['ativos.getSelect']
		},
		getMantenedores(){
			return this.$store.getters['mantenedores.get']
		},
		getMembros(){
			console.log(this.$store.getters['membros.get'].filter(e=>e.level_permission < 2))
			return this.$store.getters['membros.get'].filter(e=>e.level_permission < 2)
		},
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		},
		getSegmentos(){
			return this.$store.getters['admin.getSegmentos']
		},
        filtroDescription: {
            get(){
				return this.getFiltro['description']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {description: value}])
            }
        },
		filtroAtivo: {
            get(){
			    return this.getFiltro[this.aba.tipo == 'ativos' ? 'parentAtivoId' : 'ativoId']
            },
            set(value){
				if (this.aba.tipo == 'ativos')  this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {parentAtivoId: value}])
				if (this.aba.tipo == 'reports') this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {ativoId: value}])
            }
        },
		filtroResponsavel: {
            get(){
				return this.getFiltro['responsibleId']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {responsibleId: value}])
            }
        },
		filtroMembro: {
            get(){
				return this.getFiltro['assignedTo']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {assignedTo: value}])
            }
        },
        filtroSegmento: {
            get(){
				return this.getFiltro['segmentId']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {segmentId: value}])
            }
        },
        filtroBase: {
            get(){
				return this.getFiltro['base']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {base: value}])
            }
        },
		filtroPrivacidade: {
            get(){
				return this.getFiltro['public']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {public: value}])
            }
        },
		filtroModelo: {
            get(){
				return this.getFiltro['isModel']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {isModel: value}])
            }
        },
		filtroGeneralState: {
            get(){
				return this.getFiltro['generalState']
            },
            set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {generalState: value}])
			}
		},
		currentPage: {
			get(){
				return this.getFiltro['page']
			},
			set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {page: value}])
			}
		},
		lastPage(){
			const last = this.$store.getters[`${this.aba.tipo}.getLastPage`]
			if (last == 1 && this.currentPage != 1 ){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {page: 1}])
			}
			if (last) return last
			else return 1
		}
	},
	watch: {
		getFiltro(){
			this.$store.dispatch(`${this.aba.tipo}.load`, this.aba.tipo)
		}
	},
	methods:{
		capitalizeFirstLetter(string) {
			return string.charAt(0).toUpperCase() + string.slice(1);
		},
		searchAtivos(value){
			if (!value || value.length == 0 ) return
			this.searching_ativos_value = value
			if (this.searching_ativos) return
			this.searching_ativos = true
			setTimeout(()=>{
				this.searching_ativos = false
				this.$store.dispatch("ativos.loadSelect", this.searching_ativos_value);
			},1000)
		},
		clearFiltro(){
			this.limpar = false
			this.$store.dispatch(`${this.aba.tipo}.load`,{page: 1})
		},
	}
}
</script>

<style scoped>
.tabela-geral-filtro {
	width: 100%;
	background-color: var(--c1);
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 5px;
	border-radius: 10px;
	/*height: 50px;*/
}
.filtros{
	display: flex;
	flex-direction: row;
}

</style>