<template>
  	<div class="system-dashboard">
		<div class="dashboard-dimensao">
			<div class="dashboard-dimensao-card" v-for="(card,index) in cards_dimensao" :key="index">
				<span> 
					<h1 class="dashboard-dimensao-card-number">{{getResumo['get_company_resume'] ? getResumo['get_company_resume'][card.key] : ''}}</h1>
					<p class="dashboard-dimensao-card-name">{{card.label}}</p>
				</span>
				<va-icon :name="card.icon" size="large" color="azul"></va-icon>
			</div>
		</div>
		<div class="dashboard-fatos">
			<div class="dashboard-fatos-card" v-for="(card,index) in cards_fatos" :key="index">
				<DashboardCard :card="card" :resume="getResumo && getResumo[card.rota] ? getResumo[card.rota][0] : {}"/>
			</div>
		</div>
	</div>
</template>

<script>
import DashboardCard from '../components/Dashboard/DashboardCard.vue'
export default {
  	name: "SystemDashboard",
	components: {
		   DashboardCard
  	},
	data() {
		return {
			cards_dimensao: [
				{key: 'ativos_count', label: 'Ativos', icon: 'devices_other'},
				{key: 'plans_count', label: 'Planos', icon: 'assignment'},
				{key: 'checklists_count', label: 'Checklists', icon: 'checklist'},
				{key: 'members_count', label: 'Membros', icon: 'person'},
				{key: 'maintainers_count', label: 'Mantenedores', icon: 'construction'}
			
			],
			cards_fatos: [
				{title: 'Chamados', rota: 'get_calleds_resume'},
				{title: 'Ordens de Serviço', rota: 'get_service_resume'},
				{title: 'Reports', rota: 'get_reports_resume'},
				{title: 'Inspeções', rota: 'get_inspections_resume'},
			]
		}
	},
	computed:{
		getResumo(){
			return this.$store.getters['dashboard.getResumo']
		},
	},
	mounted(){
		this.$store.dispatch('dashboard.loadResumo', 'get_company_resume')
		for (let i = 0; i < this.cards_fatos.length; i++) {
			this.$store.dispatch('dashboard.loadResumo', this.cards_fatos[i].rota)
		}
	}
};
</script>

<style scoped>
	.system-dashboard{
		height: 100%;
	}
	.system-dashboard > div {
		display: flex;
	}
	.dashboard-dimensao{
		height: 18%;
		width: 100%;
		padding-top: 10px;
	}
	.dashboard-dimensao-card{
		width: 20%;
		margin: 0 5px;
		background-color: var(--c1);
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		border-radius: 20px ;
	}
	.dashboard-dimensao-card-number{
		font-size: 3.0rem;
		color: var(--branco);
	}
	.dashboard-dimensao-card-name{
		font-size: 1.0rem;
		color: var(--c3);
	}
	.dashboard-fatos{
		height: 80%;
		width: 100%;
		padding-top: 10px;
	}
	.dashboard-fatos-card{
		width: 25%;
		margin: 0 5px;
		padding: 5px;
		background-color: var(--c1);
		border-radius: 20px ;
	}
</style>
