<template>
	<div class="user-imagecard">
		<img v-if="getUserPhoto" :src="getUserPhoto" alt="" class="user-imagecard-img" />
		<va-icon v-else name="person" size="6rem"></va-icon>
		{{getUserName}}
		<va-button class="user-imagecard-upload" @click="upload" color="verde" text-color="cinza">Alterar foto de perfil</va-button>
	</div>
	
</template>

<script>
//import { url } from '@/configs/global'
export default {
	name: 'UserImagecard',
	data () {
		return {
			file: [],
			show_upload: false
		}
	},
    computed: {
        getUserPhoto(){
            return this.$store.getters['usuario.get'].photo
        },
        getUserName(){
            return this.$store.getters['usuario.get'].name
        },
    },
	methods: {
		upload(){
			this.$store.dispatch('evidencias.show', this.$store.getters['usuario.get'])
		}
	},
}
</script>

<style scoped>
.user-imagecard {
	grid-area: user-imagecard;
	margin-bottom: 10px;
	padding: 10px;
	display: flex;
	/*justify-content: space-between;*/
	background-color: var(--c1);
	align-items: center;
	color: white;
	position: relative;
}
.user-imagecard-img{
	max-height: 100%;
	width: 6rem;
	margin-right: 30px;
}
.user-imagecard-upload{
	position: absolute;
	right: 30px;
}
</style>