<template>
<div class="user-passwordcard">
	<div class="user-passwordcard-title">
		Alterar a Senha
	</div>
	<div class="user-passwordcard-content">
		<va-input class="user-passwordcard-input" color="dark" type="password" v-model="SENHA_ATUAL" label="SENHA ATUAL"/>
		<va-input class="user-passwordcard-input" color="dark" type="password" v-model="SENHA" label="SENHA" :rules="[(v) => (v.length >= 6 || createNew == false) || 'Deve conter no mínimo 6 caracteres']"/>
		<!-- <va-input color="cinza" type="password" class="mb-3" label="Senha" v-model="SENHA"  @keyup.enter="confirm" :rules="[(v) => (v.length >= 6 || createNew == false) || 'Deve conter no mínimo 6 caracteres']" ></va-input> -->
		<va-input class="user-passwordcard-input mb-4" color="dark" type="password" :success="CONFIRMACAO != null && CONFIRMACAO == SENHA" :error="CONFIRMACAO != null && CONFIRMACAO != SENHA" v-model="CONFIRMACAO" label="CONFIRMAÇÃO"/>
		<va-button type="submit" @click="updatePassword" color="verde" text-color="cinza" >Atualizar senha</va-button>
	</div>
</div>
</template>

<script>
import Usuario from '../../models/usuario'
export default {
	name: 'UserPasswordCard',
	data () {
		return {
			SENHA: null,
			CONFIRMACAO: null,
			SENHA_ATUAL: null
		}
	},
    computed: {
        
    },
	methods:{
		async updatePassword(){
			if (this.SENHA == this.CONFIRMACAO){
				this.$store.dispatch("loading.show");
				try {
					await Usuario.reset_password(this.axios, {
						new_password: this.SENHA, 
						current_password: this.SENHA_ATUAL
					})
					this.$store.dispatch('toast.success', "Senha alterada!");
				} catch (error) {
					this.$store.dispatch('toast.error', error);
				}
			}else{
				this.$store.dispatch('toast.error', "Senha e Confirmação não conferem");
			}
			this.$store.dispatch("loading.hide");
		}
	}
}
</script>

<style scoped>
.user-passwordcard {
	grid-area: user-passwordcard;
	background-color: var(--c1);
}
.user-passwordcard-content{
	width: 100%;
	padding: 30px;
}
.user-passwordcard-title{
	background-color: var(--c4);
	width: 100%;
	color: white;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 15px;
}
.user-passwordcard-input{
	width: 100% !important;
	margin-bottom: 10px;
}


@media only screen and (max-width: 600px) {
  .user-passwordcard {
    margin-top: 10px;
	margin-bottom: 10px;
  }
}
</style>