export default {
    state: () => ({
        objeto: { },
        show: false,
    }),
    mutations: {
        'comentarios.set': function(state, {objeto, show, }){ 
            if (objeto != undefined)    state.objeto   = objeto
            if (show != undefined)      state.show     = show
        }
    },
    getters: {
        'comentarios.getObjeto': function(state){
            return state.objeto
        },
        'comentarios.getShow': function(state){
            return state.show
        }
    },
    actions: {
        'comentarios.open': function({commit, }, objeto){
            commit('comentarios.set', { show: true, objeto: objeto, })
        },
		'comentarios.change': function( {commit}, value){
            commit('comentarios.set', { show: value })
        },
    }
}