<template>
	<FloatButton @click="loadData" icon="refresh" type="vuestic" v-if="!['dashboard','principal','relatorios'].includes(aba.tipo)"/>
    <div icon="autorenew" class="system-content">
        <div class="router-view-content" :style="vIF && false ? 'height: 95%': 'height: 100%' " ><router-view></router-view></div>
  	</div>
</template>

<script>
import FloatButton from '@/utils/FloatButton'
export default {
    name: 'SystemContent',
    components:{FloatButton, },
    computed: {
		aba(){
			const index = this.$store.getters['abas.getIndices'][this.$route.path]
			const abas = this.$store.getters['abas.getAbas'][this.$route.path]
			return index != undefined && abas != undefined ? abas[index] : {}
		},
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		},
    },
    methods: {
		loadData() {
			if (this.aba.callback && this.aba.tipo) this.$store.dispatch(this.aba.callback, this.aba.tipo)
		},
    },
	watch: {
		aba(){
			this.loadData()
		}
	},
    mounted() {
		console.log('INICIO MOUNTED')
        this.$store.dispatch('abas.set', [this.$route.path, 0])
        const tipos = ["mantenedores", "projetos", 'setores', 'projetos', 'planos', 'membros']
		for (let i = 0; i < tipos.length; i++) {
			this.$store.dispatch(`${tipos[i]}.load`);
		}
        this.$store.dispatch("categorias.load", "maintenance");
		this.$store.dispatch("categorias.load", "ativos");
		this.$store.dispatch("ativos.loadCategorias");
		this.$store.dispatch("ativos.loadWithChildren");
		console.log('FIM MOUNTED')
    },
}
</script>

<style scoped>
.system-content{
    grid-area: system-content;
    background-color: var(--cinza);
    overflow-y: auto;
    padding: 10px;
    padding-top: 0;
}
.router-view-content{
    height: 95%;
}
.titulo{
    height: 5%;
	background-color: var(--c1);
	display: flex;
	justify-content: space-between;
	align-items: stretch;
}
.nomes {
	display: flex;
	flex-wrap: wrap;
}
.filtro {
	display: flex;
    align-items: center;
}
.nomes-card {
	padding: 10px 25px;
	color: var(--branco);
	display: flex;
	justify-content: center;
	align-items: center;
    /*border-radius: 0px 15px 0px 0px;*/
    height: 100%;
    font-size: 0.9rem;
}
.nomes-card:hover {
	cursor: pointer;
	opacity: 0.8;
}
#background1{
    background-color: var(--c4);
    font-style: italic;
}
#background2{
	background-color: var(--c1);
}
@media only screen and (max-width: 800px) {
	.titulo, .nomes{
        /*flex-wrap: wrap;*/
        flex-direction: column;
        justify-content: left;
		font-size: 1em;
	}
	.nomes-card {
		padding: 8px 8px;
       
	}
    .filtro{
        display: none;
    }

}
</style>