<template>
	<div class="dashboard-card" >
		<div>
		<div class="card-title">
			<h1> {{card.title}} </h1>
			<p v-if="card.title == 'Chamados'"> Total de  {{resume.total_items}} chamados</p>
			<p v-if="card.title == 'Ordens de Serviço'"> Total de  {{resume.total_services}} serviços</p>
			<p v-if="card.title == 'Reports'"> Total de  {{resume.total_reports}} reports</p>
			<p v-if="card.title == 'Inspeções'"> Total de  {{resume.total_inspections}} inspeções</p>
		</div>
		<div class="card-items">
			<span v-for="dado in getDados()" :key="dado.subtitle" >
				<DashboardCardItems  :icon="dado.icon" :subtitle="dado.subtitle" :number="dado.number" :color="dado.color" :aba="aba" :generalState="dado.generalState"/>
			</span>
		</div>
		</div>
		<div class="chart">
			<DashboardChartPie :resume="resume" :dados="getDados()" :color="getColor()"/>
		</div>
	</div>
</template>

<script>
import DashboardCardItems from './DashboardCardItems.vue'
import DashboardChartPie from './DashboardChartPie.vue'
import Chamado from '../../models/chamado'
import Servico from '../../models/servico'
import Report from '../../models/report'
import Inspecao from '../../models/inspecao'
export default {
	name: 'DashboardCard',
	props : ['card','resume'],
	components: {
		DashboardCardItems,
		DashboardChartPie
  	},
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		}, 
		aba(){
			const title = this.card.title
			const abas  = this.$store.getters["abas.getAbas"]['/sistema/servicos']
			switch (title) {
				case 'Chamados':
					return abas[0]
				case 'Ordens de Serviço':
					return abas[1]
				case 'Reports':
					return abas[2]
				case 'Inspeções':
					return abas[3]
				default:
					return {}
			}
		}
    }, 
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
		getDados(){
			let DADOS = []
			if(this.card.title == 'Chamados') DADOS = Chamado.DADOS
			else if(this.card.title == 'Ordens de Serviço') DADOS = Servico.DADOS
			else if(this.card.title == 'Reports') DADOS = Report.DADOS
			else if(this.card.title == 'Inspeções') DADOS = Inspecao.DADOS
			return DADOS.map((e)=>{
				return {
					...e,
					number: e.number(this.resume)
				}
			})
		},
		getColor(){
			let color = []
			if(this.card.title == 'Chamados') color = ["#3CDE93","#FFDDAA","#3CB3DE", "#F85B40"]
			else if(this.card.title == 'Ordens de Serviço') color = ["#3CDE93","#FFDDAA","#3CB3DE", "#F85B40", "#181a21", "#BA55D3"]
			else if(this.card.title == 'Reports') color = ["#3CDE93","#FFDDAA", "#181a21"]
			else if(this.card.title == 'Inspeções') color = ["#3CDE93","#FFDDAA", "#181a21"]
			return color
		}
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.dashboard-card {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.card-title {
	color: #CCCCCC90;
	text-align: center;
	padding: 5px;
}

.card-title > h1 {
	font-size: 1.1rem;
	color: var(--c3);
}

.card-items {
	width: 100%;
}

.chart{
	width: 100%;
	height: 220px;
}
</style>