import { url, get_queries } from '@/configs/global'
import Empresa from "@/models/empresa";
import Usuario from '@/models/usuario'
import Categoria from '@/models/categoria'
import Segmento from '@/models/segmento'

export default {
    state: () => ({
        LOGIN: [],
        usuarios: [], 
        empresas: [],
        categorias: [],
        segmentos: [],
        last_page: 1
    }),
    mutations: {
        'admin.set': function(state, {membros, permissao, last_page, empresas, categorias, segmentos}){
            if(membros != undefined) state.usuarios = membros
            if(permissao != undefined) state.permissao = permissao
            if(last_page != undefined) state.last_page = last_page
            if(empresas != undefined) state.empresas = empresas
            if(categorias != undefined) state.categorias = categorias
            if(segmentos != undefined) state.segmentos = segmentos
        },
    },
    getters: {
        'admin.getUsuarios': function(state){
            return state.usuarios
        },
        'admin.getEmpresas': function(state){
            return state.empresas
        },
        'admin.getCategorias': function(state){
            return state.categorias
        },
        'admin.getSegmentos': function(state){
            return state.segmentos
        },
        'admin-usuarios.getLastPage': function(state){
            return state.last_page
        },
        'admin-empresas.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'admin-usuarios.load': function( { dispatch, getters, commit} , args){
            dispatch('loading.show');
            console.log("loading admin-usuarios")
            
			this.axios.get(`${url}/api/admin/get_all_users?${get_queries(getters, args)}`)
			.then((response) => {
                const membros = response.data.data.data.map(obj => new Usuario(obj, this.axios))
                const membro = membros.filter(e=>e.id==getters['usuario.get'].id)
                commit('admin.set', {
                    membros: membros,
                    permissao: membro.length > 0 ? membro[0].level_permission : 999,
                    last_page: response.data.data.last_page,
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>dispatch('loading.hide'))
        },
        'admin-empresas.load': function( { dispatch, getters, commit} , args){
            dispatch('loading.show');
            console.log("loading admin-empresas")
            
			this.axios.get(`${url}/api/admin/get_all_companies?${get_queries(getters, args)}`)
			.then((response) => {
                const empresas = response.data.data.data.map(obj => new Empresa(obj, this.axios))
                commit('admin.set', {
                    empresas: empresas,
                    last_page: response.data.data.last_page,
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>dispatch('loading.hide'))
        },
        'admin-categorias.load': async function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
            console.log("loading admin-categorias")

            const filtros = getters['abas.getFiltro']
            const filtro  = filtros && filtros['admin-categorias'] ? filtros['admin-categorias'] : {}

            let tipos = [`get_all_categories/ativos`, `get_all_service_maintenance_type`, ]

            if(filtro.base == 'ativos'){
                tipos = [`get_all_categories/ativos`]
            }
            if(filtro.base == 'serviços'){
                tipos = [`get_all_service_maintenance_type`]
            }
            
            let categorias = []
            for (let i = 0; i < tipos.length; i++) {
                try {
                    const tipo = tipos[i];
                    const response = await this.axios.get(`${url}/api/admin/${tipo}?${get_queries(getters, args)}`)
                    const dados = response.data.data.map(e => new Categoria({
                        ...e,
                        base: tipo == 'get_all_service_maintenance_type' ? 'serviços' : e.base
                    }, this.axios))
                    categorias.push( ...dados )
                } catch (err) {
                    dispatch('toast.error', err);
                }
            }
            commit('admin.set', { categorias, last_page: 1, })
            dispatch('loading.hide');
        },
        'admin-segmentos.load': function( { dispatch,  commit}){
            dispatch('loading.show');
            console.log("loading admin-segmentos")
            
			this.axios.get(`${url}/api/get_segments`)
			.then((response) => {
                commit('admin.set', { segmentos: response.data.data.map(obj => new Segmento(obj, this.axios)) })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>dispatch('loading.hide'))
        },
    }
}