<template>
	<FloatButton v-if="aba.model" @click="add()" icon="add" type="vuestic" style="bottom: 90px;"/>
   <div class="system-empresa">
		<div class="system-empresa-div" v-if="this.aba.tipo=='principal'">
			<!--{{getObjetos}}-->
			<EmpresaInfo :empresa="getObjetos"/>
		</div>
		<div class="system-empresa-div" v-else-if="this.aba.tipo=='work-shift'">
			<EmpresaWorkShift :empresa="getObjetos"/>
		</div>
		<div class="system-empresa-div" v-else-if="this.aba.tipo=='relatorios'">
			<EmpresaRelatorios :objetos="getObjetos"/>
		</div>
		<div class="system-empresa-div" v-else-if="this.aba.tipo">
			<Estatistica v-if="aba.tipo == 'mantenedores'" :objetos="getObjetos" :aba="this.aba" img='servicos.svg' caption='"O segredo de um grande sucesso está no trabalho de uma grande equipe."' />
			<TabelaGeral :aba="this.aba" :objetos="getObjetos"  />
		</div>
   </div>
</template>

<script>
import FloatButton from '@/utils/FloatButton'
import TabelaGeral from '@/utils/TabelaGeral'
import Estatistica from '@/utils/Estatistica'
import EmpresaInfo from '@/components/Empresa/EmpresaInfo'
import EmpresaWorkShift from '@/components/Empresa/EmpresaWorkShift'
import EmpresaRelatorios from '@/components/Empresa/EmpresaRelatorios'
export default {
    name: 'SystemEmpresa',
	components:{FloatButton, TabelaGeral , Estatistica, EmpresaInfo, EmpresaRelatorios, EmpresaWorkShift },
	data: () => ({
		
    }),
	computed: {
		getObjetos(){
			//console.log('work-shift')
			return  this.$store.getters[this.aba.getters]
		},
		aba(){
			const index = this.$store.getters['abas.getIndices'][this.$route.path]
			const abas = this.$store.getters['abas.getAbas'][this.$route.path]
			return index != undefined ? abas[index] : {}
		},
	},
	methods:{
		add(){
			let permissao =  this.$store.getters['membro.getPermissao'];
			console.log(this.aba)
			if(permissao < 2 || (permissao < 100 && this.aba.tipo != 'membros')){
				this.$store.dispatch('edit-modal.editShow', {
					objeto: this.aba.model,
					callback: this.aba.callback
				})
			}
			else{
				this.$store.dispatch('toast.error', 'Não autorizado!')
			}
		}
	}
}
</script>

<style scoped>
.system-empresa {
	margin: 0px;
	width: 100%;
	height: 100%;
}
.system-empresa-div{
	height: 100%;
	/*padding-top: 10px;*/
}
</style>