<template>
    <div class="chat" :id="$store.getters['usuario.get'].id == note.created_by ? 'align-right' : '' " :style="note.deleted == 1 ? 'background-color: var(--c4);': ''">
    <va-inner-loading  :loading="loading">
        <div class="chat-title">
            {{getMembro(note.created_by).name}}
            <va-button v-if="$store.getters['usuario.get'].id == note.created_by && !note.deleted" icon="delete" size="small" color="laranja" @click="remove"/>
            <span v-if="note.deleted" style="font-style: italic; color: var(--c3);" >Removido</span>
        </div>
        <div v-if="!note.deleted" class="chat-msg">{{note.description}}</div>
        <div class="chat-time">{{note.created_date.toLocaleString()}}</div>
    </va-inner-loading>
    </div>
</template> 

<script>
export default {
    name: 'ComentariosMsg',
    props:{
        note: Object,
        objeto: Object
    },
    data() {
        return {
            loading: false,
        }
    },
    methods: {
        async remove(){
            if (!confirm('Confirmar?')) return
            try {
                this.loading = true;
                await this.note.delete({ getters: this.$store.getters })
                await this.objeto.getNotes({ getters: this.$store.getters })
                this.$store.dispatch('toast.success', 'Removido!')
            } catch (error) {
                this.$store.dispatch('toast.error',error)
            }
            this.loading = false;
        },
        getMembro(id){
			let membros =  this.$store.getters['membros.get'].filter(e => e.id == id);
            if (membros.length > 0)
			    return membros[0]
            else
                return {}
        },
    },
}
</script>

<style scoped>
.chat{
    width: 80%;
    background-color: var(--c1);
    margin: 2.5px 0;
    padding: 10px;
    border-radius: 10px;
}
#align-right{
    align-self: flex-end;
}
.chat-title{
    color: var(--verde);
    padding: 5px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.chat-msg{
    padding: 5px;
    color: var(--c3);
    white-space: pre-line;
}
.chat-time{
    text-align: right;
    padding: 5px;
    color: var(--c3);
    font-size: 0.9em;
    white-space: pre-line;
}

</style>