<template>
	<div class="empresa-info" >
		<div class="empresa-info-geral">
			<div class="empresa-title">Informações Gerais </div>
			<div class="empresa-imagecard">
				<div class="container" >
					<img v-if="getEmpresa.url_image" :src="getEmpresa.getImageURL()"> 
					<img  v-else src="@/assets/empresa_default.png" class="image"> 
					<div class="overlay">
						<va-button size="small" color="verde" text-color="cinza" class="mb-3" @click="showImg(getEmpresa.getImageURL())">Abrir</va-button>
						<va-file-upload v-model="file" color="verde" type="single" @input="input" file-types=".jpg,.png,.svg,.eps,.PNG,.jpeg"/>
					</div>
				</div>
				<va-modal v-model="show_upload" :mobile-fullscreen="false" color="verde"  @ok="enviar" ok-text="Enviar" cancel-text="Cancelar">
				</va-modal>
				<div style="display:flex; flex-direction: column">
					<va-input v-model="empresa.company_name" :style="'margin: 10px'" color="cinza" label="RAZÃO SOCIAL" type="name"/>
					<va-input v-model="empresa.CNPJ" :style="'margin: 10px; color: red'" color="cinza" label="CNPJ" placeholder="XX.XXX.XXX/0001-XX"/>
					<div style="margin-left: 20px; margin-top: 20px;"><va-button color="verde" text-color="cinza" @click="edit()" >Atualizar</va-button></div>
				</div>
			</div>
		</div>
		<div class="empresa-dashboard-financeiro">
			<div class="empresa-title"> Controle de Uso</div>
			<div class="empresa-dashboard-financeiro-conteudo">
				<div class="empresa-progress-bar">
					<div class="empresa-progress-bar-title">Ativos</div>
					<va-progress-bar :model-value="getEmpresaStatus.ativos.percent" color="azul" size="15px">
						<div class="empresa-progress-bar-text"> 
							<span>{{getEmpresaStatus.ativos.percent}} %</span>
							<span>{{getEmpresaStatus.ativos.value}} / {{getEmpresaStatus.ativos.allowed}}</span>
						</div> 
						</va-progress-bar>
					<div class="empresa-progress-bar-title" >Membros</div>
					<va-progress-bar :model-value="getEmpresaStatus.membros.percent" color="azul" size="15px">
						<div class="empresa-progress-bar-text"> 
							<span>{{getEmpresaStatus.membros.percent}} %</span>
							<span>{{getEmpresaStatus.membros.value}} / {{getEmpresaStatus.membros.allowed}}</span>
						</div> 
					</va-progress-bar>
					<div class="empresa-progress-bar-title">Memória</div>
					<va-progress-bar :model-value="getEmpresaStatus.memoria.percent" color="azul" size="15px">
						<div class="empresa-progress-bar-text"> 
							<span>{{getEmpresaStatus.memoria.percent}} %</span>
							<span>{{getEmpresaStatus.memoria.value}} MB/ {{getEmpresaStatus.memoria.allowed}} MB</span>
						</div> 
					</va-progress-bar>
					<div class="chartMemoria"><DashboardChartPie :dados="dadosMemoria"/></div>
				</div>
				<!-- <div class="empresa-progress-bar">
					<div class="plano">
						<div class="empresa-progress-bar-title"> Plano </div>
					</div>
					<div class="plano">
						<div class="empresa-progress-bar-title"> <span>Pagamento</span> 
						<va-popover message="Histórico de faturas"><va-icon   class="ml-2" name="info" size="medium"/> </va-popover></div>
						<div class="empresa-progress-bar-text"> Última fatura: R$ 200,00</div>
						<div class="empresa-progress-bar-text"> Próximo vencimento: 10/11/2022</div>
						<div style="width: 100%; padding: 20px 15px; display: flex; flex-direction: row; justify-content: space-between;">
							<div style="text-align: center;"><va-button  icon="pix" size="50px" color="cinza" text-color="c3"/> <div>Pix</div> </div>
							<div style="text-align: center;"><va-button  icon="qr_code_2" size="50px" color="cinza" text-color="c3"/> <div>Boleto</div> </div>
							<div style="text-align: center;"><va-button  icon="payment" size="50px" color="cinza" text-color="c3"/> <div>Cartão </div> </div>
						</div>
					</div>
				</div>  -->
			</div>
		</div>

		<!-- <div class="empresa-info-planos">
			<div class="empresa-work-shift-title">Planos </div>
			<EmpresaInfoPlanos/>
		</div>-->
	</div>
	
</template>

<script>
//import {url} from '../../configs/global'
import Usuario from '../../models/usuario'
import DashboardChartPie from './DashboardChartPie.vue'
// import EmpresaInfoPlanos from './EmpresaInfoPlanos'
export default {
	name: 'EmpresaInfo',
	//components:{EmpresaInfoPlanos},
	data: () => ({
		empresa: {},
		file: [],
		show_upload: false, 
    }),
	components: {
		DashboardChartPie
  	},
    computed: {
        getEmpresa(){
			console.log(this.$store.getters['empresa.get'])
			return this.$store.getters['empresa.get']
		},
		getPermissao(){
			return this.$store.getters['membro.getPermissao']
		},
		getEmpresaStatus(){
			let dados = []
			let data = { memoria: {percent: 0}, ativos: { percent: 0}, membros: { percent: 0}}
			dados = this.getStatus
			if(dados.contador_ativos != undefined){
				data = {
						memoria:{ percent: (dados.armazenamento['ativos'] + dados.armazenamento['services'] + dados.armazenamento['reports'])/dados.armazenamento['allowed']*100, value: (dados.armazenamento['ativos'] + dados.armazenamento['services'] + dados.armazenamento['reports']) / 1000000, allowed: dados.armazenamento['allowed']/1000000},

						ativos: { percent: (dados.contador_ativos['value'])/dados.contador_ativos['allowed']*100, value: dados.contador_ativos['value'], allowed: dados.contador_ativos['allowed'] },
						
						membros: { percent: (dados.contador_membros['value'])/dados.contador_membros['allowed']*100, value: dados.contador_membros['value'], allowed: dados.contador_membros['allowed'] },
						}
			}
			return data
		},
		getStatus(){
			return this.$store.getters['empresaStatus.get']
		},
		dadosMemoria(){
			let dados = []
			if(this.getStatus.armazenamento != undefined){
				dados =  [
					{ value: this.getStatus.armazenamento['ativos']/1000000, name: 'Ativos' },
					{ value: this.getStatus.armazenamento['services']/1000000, name: 'Serviços' },
					{ value: this.getStatus.armazenamento['reports']/1000000, name: 'Reports' }
				]
			}
			console.log(dados)
			return dados
		},
    },
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
			this.$store.dispatch('empresaStatus.load')
		},
		edit(){
			if (this.getPermissao < 100){
				this.$store.dispatch('loading.show')
				this.empresa.update({
					getters: this.$store.getters
				}).then(async () => {
					this.$store.dispatch('toast.success', 'Atualizado!')
				}).catch(function (error) {
					console.log(error)
					this.$store.dispatch('toast.error', 'Erro!')
				}).finally(()=>this.$store.dispatch('loading.hide'))
			} else this.$store.dispatch('toast.error', 'Não autorizado!')
		},
		showImg(img){
			window.open(img, "_blank");	
		},
		input: function(){
			this.show_upload = true
		},
		enviar: function() {
			this.empresa.savePhoto({
				getters: this.$store.getters,
				blob: this.file[0]
			}).then(async () => {
				this.file = []
				this.$store.dispatch('toast.success', 'Atualizado!')
				this.$store.dispatch('empresa.set', await Usuario.getAllCompanies(this.axios));
			}).catch((err)=>{
				console.log(err, err.data)
				this.$store.dispatch('toast.error', 'Erro!');
			})
		},
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

