        "empresa-info-geral empresa-dashboard-financeiro"
        /*"system-menu system-footer"*/;;
}

.empresa-imagecard {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	/* align-items: center; */
	text-align: left;
}

