	font-size: 1.2em;
	padding:  15px 0px;
	color: var(--branco);
}

.empresa-dashboard-financeiro-conteudo {
	display: flex;
	flex-direction: row;
}

.empresa-progress-bar-text {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	color: var(--c3);
	font-weight: normal;
}

.chartMemoria {
	height: 300px;
	padding-top: 30px;
	/*background-color: blue;*/
}

