	font-size: 1.5em;
	padding: 15px;
	color: var(--branco);
	/*font-weight: bold;*/
}

.plano{
	height: 45%;
	background-color: var(--cinza);
	color: var(--c3);
	border-radius: 20px;
	padding: 15px;
	margin-bottom: 50px;
}

.container{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 5px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #3C3F4A99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container:hover .overlay {
  opacity: 1;
}
.image{
	width: 250px;
	height: 250px;
}
</style>