<template>
	<FloatButton v-if="aba.model" @click="add" icon="add" type="vuestic" style="bottom: 90px" />

	<!-- {{getObjetos}} -->
	<TabelaGeral :aba="aba" :objetos="getObjetos ? getObjetos : []"/>
</template>

<script>
import FloatButton from "@/utils/FloatButton";
import TabelaGeral from '../utils/TabelaGeral'
export default {
	name: "SystemAdmin",
	components: {
		FloatButton, TabelaGeral
	},
	data: () => ({
		open: false,
		showAll: false
	}),
	computed: {
		getObjetos(){
			return this.$store.getters[this.aba.getters]
		},
	
		aba() {
			const index = this.$store.getters["abas.getIndices"][this.$route.path];
			const abas = this.$store.getters["abas.getAbas"][this.$route.path];
			return index != undefined && abas ? abas[index] : {};
		},
		id_empresa(){
			return this.$store.getters['empresa.get'].id
		},
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		}
	},
	methods: {
		add() {
			this.$store.dispatch("edit-modal.editShow", {
				objeto: this.aba.model,
				callback: this.aba.callback,
			});
		},
	},
	mounted() {
		if (this.aba.tipo != 'admin-segmentos') this.$store.dispatch("admin-segmentos.load");
	},
};
</script>

<style scoped>
.system-admin {
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
