<template>
    <va-modal v-model="show" :hide-default-actions="true" :mobile-fullscreen="false" max-width="100vw">
		<div class="modal-inspecoes">
			<div class="modal-inspecoes-header">
				<div>{{getInspecao.description}}</div>
				<div class="modal-process-bar">
					<div :style="getStyle(percentInspection(getSections),1)" >{{percentInspection(getSections)}} % </div>
					<div :style="getStyle(percentInspection(getSections),0)" ></div>
				</div>
			</div>
			<div class="modal-inspecoes-sutitle" v-if="getInspecao.finished_at != null"> Concluída por {{getMembro(getInspecao.finished_by)}} às {{getInspecao.finished_at}}</div>

			<div v-for="section in getSections" :key="section.id" class="modal-inspecoes-objeto">
				<div class="objeto-titulo" @click="section.dropdown_ativado = !section.dropdown_ativado">
					<div> {{section.description}} </div>
					<va-progress-circle :modelValue="percentSection(section.items)" color="verde" size="40px"  :thickness="0.1">
						{{ percentSection(section.items) + '%' }}
					</va-progress-circle>
				</div>
				
				<div v-if="section.dropdown_ativado" class="objeto-conteudo">
					<div v-for="item in section.items" :key="item.id">
						<div class="sub-itens">
							{{item.description}}
							<div class="icones">
								<span>
									<va-popover message="Ver evidências">
										<va-icon class="mr-5" size="small" name="image" color="secondary" @click="item.dropdownEvidencias = !item.dropdownEvidencias"/>
									</va-popover>
									<va-icon class="mr-5" size="small" name="speaker_notes" color="secondary" @click="item.dropdown_ativado = !item.dropdown_ativado" />
								</span>
								<span style="padding: 2px;">
									<va-popover message="Confome">
										<va-icon v-if="item.status == 0" size="small" name="thumb_up" color="verde"/>
										<va-icon v-if="item.status != 0" size="small" name="thumb_up" color="cinza"/>
									</va-popover>
								</span>
								<span style="padding: 2px;">
									<va-popover message="Não confome">
										<va-icon v-if="item.status == 1" size="small" name="thumb_down" color="laranja"/>
										<va-icon v-if="item.status != 1" size="small" name="thumb_down" color="cinza"/>
									</va-popover>
								</span>
								<span style="padding: 2px;">
									<va-popover message="Não aplicável.">
										<va-icon v-if="item.status == 2" size="small" name="cancel" color="azul"/>
										<va-icon v-if="item.status != 2" size="small" name="cancel" color="cinza"/>
									</va-popover>
								</span>
							</div>
						</div>
						<div v-if="item.dropdown_ativado" class="comentarios">
							<div>Comentários</div>
							{{item.note}}
						</div>
						<div v-if="item.dropdownEvidencias" class="comentarios">
							<div v-if="getEvidencias(item).length != 0">Evidências</div>
							<div v-if="getEvidencias(item).length == 0">Não há evidências deste item.</div>
							<div v-if="getEvidencias(item).length != 0" style="display:flex; flex-wrap: wrap; flex-direction: row;">
								<div class="container" v-for="url in getEvidencias(item)" :key="url">
									<img :src="url" class="image">
									<div class="overlay">
										<va-button size="small" color="verde" text-color="cinza" class="mb-3" @click="showImg(url)">Abrir</va-button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

				<!--{{section.items}}-->
			</div>
		</div>
	
    </va-modal>
</template>

<script>
export default {
	name: "ModalInspecoes",
	methods: {
		getMembro(finished_by){
			const membros = this.$store.getters['membros.get'].filter(e=>e.id==finished_by)
			if (membros.length == 1) {
				return membros[0].name
			}
			return "NULL"
		},
		getEvidencias(item){
			const id_e = this.$store.getters['empresa.get'].id
			let evidencias = item.infos.map(info => `https://px40-api.cme-eng.com.br/thumb/inspections/${info.img}?c=${id_e}&s=${info.inspection_item_id}&h=250&w=250`)
			/*alert(evidencias)*/
			console.log(evidencias)
			return evidencias
		},
		percentSection(items){
			let total = 0 
			let conforme = 0
			for (let i = 0; i < items.length; i++) {
				if(items[i].status !=2) total = total + items[i].weight
				if(items[i].status ==0) conforme = conforme + items[i].weight
			}
			if(total != 0) return conforme/total*100
			else return 0
		},
		percentInspection(sections){
			let total = 0 
			let conforme = 0
			for (let j = 0; j < sections.length; j++) {
				for (let i = 0; i < sections[j].items.length; i++) {
					if(sections[j].items[i].status !=2) total = total + sections[j].items[i].weight
					if(sections[j].items[i].status ==0) conforme = conforme + sections[j].items[i].weight
				}
			}
			if(total != 0) return (conforme/total*100).toFixed(1)
			else return 0
		},
		getStyle(percent, conforme){
			if(conforme) return 'background-color: var(--verde); width:'+percent+'%;'
			else return 'background-color: #f85b4080; width:'+(100-percent)+'%;'
			
			//width= {{percentInspection(getSections)}}; background-color: red;"
		},
		showImg(img){
			console.log(img)
			window.open(img.replace("&h=250&w=250", ""), "_blank");	
		},

		/*update(objeto, parente={}){
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                callback: this.aba.callback,
				id_parente: parente.id
			})
		},*/
	},
	data () {
		return {
			value: 75,
    	}
	},
	computed: {
		show: {
			get(){
				return this.$store.getters['inspecoes.getShow']
			},
			set(show){
				this.$store.dispatch('inspecoes.change_show', show)
			}
		},
		getSections() {
			return this.$store.getters['inspecoes.getSecoes']
		},
		getInspecao() {
			return this.$store.getters['inspecoes.getObjeto']
		},
		/*aba() {
			return this.$store.getters['documentos.getAba']
		},
		objetos() {
			return this.$store.getters['documentos.getObjetos']
		},*/
	},
}
</script>

<style scoped>
.modal-inspecoes{
	color: white;
	min-width: 60vw;
	display: flex;
	flex-direction: column;
}
.modal-inspecoes-header{
	display: flex;
	justify-content: space-between;
	padding: 5px 0px;
	text-transform: uppercase;
}

.modal-inspecoes-sutitle{
	color: var(--c3);
	font-size: 0.8em;
	margin-bottom: 10px;
}

.modal-process-bar{
	width: 200px;
	height: 20px;
	background-color: #3cde9310;
	text-align: center;
	font-size: 0.8em;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-content: space-between;
	color: var(--cinza);
}

.modal-inspecoes-objeto{
	margin-bottom: 10px;
}
.icones:hover{
	cursor: help;
}
.objeto-titulo{
	background-color: var(--c4);
	display: flex;
	padding: 0px 0px 0px 10px;
	align-items: center;
	justify-content: space-between;
}
.objeto-titulo:hover{
	cursor: pointer;
	background-color: #414551;
}
.sub-itens{
	background-color: var(--c1);
	/*margin-left: 20px;*/
	/*margin-top: 2px;*/
	/*padding: 5px;*/
	padding: 10px 10px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.objeto-conteudo{
	padding: 0px;
	background-color: var(--c1);
}

.comentarios{
	margin: 10px;
	background-color: #52576650;
	padding: 10px;
	border-radius: 10px;
	color: var(--c3);
	font-size: 0.8em;
}

.comentarios div{
	text-transform: uppercase;
	margin-bottom: 10px;
}



.container{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 5px;
	width: 250px;
	height: 250px;
}

.image{
	width: 100%;
	display: block;
}
.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #3C3F4A99;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container:hover .overlay {
  opacity: 1;
}

</style>