<template>
   	<va-modal v-model="showModal" :hide-default-actions="true" :mobile-fullscreen="false" title="" max-width="100vw">
		<div class="modal-reports">
			<ModalTitle dispatch="reports.changeShowFinish" :objeto="objeto" title="Finalizar report"/>
			<div class="modal-reports-finish" v-if="objeto.finished">Finalizado por {{getMembro(objeto.finished_by)}} em {{timestamp(objeto.finished_at)}}.</div>
			<!-- <va-switch v-model="objeto.finished" label="Finalizar este report?" left-label color="verde" class="mb-3" size="small"/> -->
			<va-select v-model="objeto.service_id" label="Serviço associado" text-by="request" value-by="id" track-by="id"  color="verde" class="mb-3" searchable @update-search="updateSearch" :options="getServices"/>
			<va-input v-model="objeto.reason_finished" label="Comentário de encerramento" color="verde"  type="textarea" class="mb-3"/>
			<va-button @click="finalizarReport()" color="verde" text-color="cinza"> Enviar</va-button>
		</div>
	</va-modal>
</template>

<script>
export default {
    name: 'ModalReportsFinish',
	data: () => ({
        showModal: false,
		option: [false, true],
		objeto : {},
    }),
	computed: {
		getShow(){
			return this.$store.getters[`reports.getShowFinish`];
		},
		getPermissao(){
			return this.$store.getters['membro.getPermissao']
		},
		getServices(){
			console.log(this.$store.getters['servicos.get'])
			return this.$store.getters['servicos.get']
		}
	},
	methods:{
		finalizarReport(){
			if (this.getPermissao < 100){
				this.objeto.finishReport({
					getters: this.$store.getters
				})
				this.$store.dispatch('toast.success', 'Report encerrado.');			
			}
			else this.$store.dispatch('toast.error', 'Não autorizado.');			
			this.showModal= false;
		},
		updateSearch(valor){
			this.$store.dispatch('servicos.load',`description=${valor}`)
		},
		getMembro(finished_by){
			const membros = this.$store.getters['membros.get'].filter(e=>e.id==finished_by)
			if (membros.length == 1) {
				return membros[0].name
			}
			return "NULL"
		},
		timestamp(time_str){
			var date = new Date(time_str)
			return date.toLocaleString('pt-BR')
		},
	},
    watch: {
        getShow: function (value) {
            this.showModal = value
			this.objeto = this.$store.getters[`reports.getFinishObjeto`];
        },
		showModal: function (value) {
            this.$store.dispatch(`reports.changeShowFinish`, value);
        },
    }
}
</script>

<style scoped>

.modal-reports{
	display: flex;
	flex-direction: column;
	min-width: 50vw;
    min-height: 75vh;
	color: var(--branco);
}
.modal-reports-finish{
	color: var(--c3);
	font-size: 0.8em;
	margin-bottom: 10px;
}


</style>