export default {
    state: () => ({
        show: false,
        objeto: {},
    }),
    mutations: {
        'assinatura.set': function(state, {show, objeto }){
            if(show != undefined) state.show = show
            if(objeto != undefined) state.objeto = objeto
        },
    },
    getters: {
        'assinatura.getShow': function(state){
            return state.show
        },
        'assinatura.getObjeto': function(state){
            return state.objeto
        },
    },
    actions: {
		'assinatura.show': function({commit}, objeto){
            commit('assinatura.set', { show: true, objeto: objeto })
        },
		'assinatura.change_show': function( {commit}, value){
            commit('assinatura.set', { show: value })
        }
    }
}