<template>
	<FloatButton v-if="aba.model" @click="add" icon="add" type="vuestic" style="bottom: 90px" />
	<!-- <FloatButton v-if="aba.tipo == 'servicos'" @click="openCalendar" icon="calendar_month" type="vuestic" style="bottom: 160px" /> -->
	<div class="system-patrimonio">
		<Estatistica
		v-if="aba.tipo == 'ativos'"
		:objetos="getObjetos"
		:aba="aba"
		img="ativos.svg"
		caption='"Se você pode tocar, cadastre aqui."'
		/>
		<TabelaGeral :objetos="getObjetos" :aba="aba" />
	</div>
	<ModalDocumentos />
</template>

<script>
import FloatButton from "@/utils/FloatButton";
import Estatistica from "@/utils/Estatistica";
import TabelaGeral from "@/utils/TabelaGeral";
import ModalDocumentos from '../components/Patrimonio/ModalDocumentos'
export default {
	name: "SystemPatrimonio",
	components: {
		FloatButton,
		Estatistica,
		TabelaGeral,
		ModalDocumentos,
	},
	data: () => ({
		open: false,
	}),
	computed: {
		getObjetos() {
			if (this.aba.tipo == "ativos") return this.$store.getters["ativos.get"];
			if (this.aba.tipo == "reports") return this.$store.getters["reports.get"];
			if (this.aba.tipo == "servicos") return this.$store.getters["servicos.get"];
			if (this.aba.tipo == "planos") return this.$store.getters["planos.get"];
			if (this.aba.tipo == "checklists") return this.$store.getters["checklists.get"];
			return []
		},
		aba() {
			const index = this.$store.getters["abas.getIndices"][this.$route.path];
			const abas = this.$store.getters["abas.getAbas"][this.$route.path];
			return index != undefined && abas ? abas[index] : {};
		},
		id_empresa(){
			return this.$store.getters['empresa.get'].id
		},
	},
	methods: {
		add() {
			let permissao =  this.$store.getters['membro.getPermissao'];
			if(permissao < 100){
				this.$store.dispatch("edit-modal.editShow", {
					objeto: this.aba.model,
					callback: this.aba.callback,
				});
			}
			else{
				this.$store.dispatch('toast.error', 'Não autorizado!')
			}
		},
		openCalendar() {
			this.$store.dispatch("servicos.calendar");
		},
	},
};
</script>

<style scoped>
.system-patrimonio {
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /*display: grid;*/
  /*grid-template-rows: 150px 1fr;*/
  /*grid-template-areas:
    "estatistica"
    "patrimonio-tabela";*/
}
.ativos-filter {
  margin-bottom: 10px;
  width: 100%;
  background-color: var(--c5) !important;
}

.ativos-filter-input {
  color: white !important;
}

.red {
  color: red;
}
</style>
