<template>
    <div class="user-dropdown">
        <div class="user-dropdown-button" @click="show = !show"  >
            <div class="user-dropdown-button-user">{{getUserName}}</div>
            <img v-if="getUserPhoto" :src="getUserPhoto" alt="" class="user-imagecard-img" />
            <va-icon v-else name="person" size="3rem"></va-icon>
        </div>
        <div class="user-dropdown-content" v-bind:id="show? 'content-show' : 'content-hide'">
            <div class="perfil"><a href @click.prevent="perfil ">Perfil <va-icon name="person" size="1.3rem"/></a></div>
            <a href @click.prevent="trocar">Workspaces<va-icon name="change_circle" size="1.3rem"/></a>
            <!-- <a href @click.prevent="remover_workspace">Remover workspace<va-icon name="delete" size="1.3rem"/></a> -->
            <a href @click.prevent="logout">Sair <va-icon name="logout" size="1.3rem"/></a>
        </div>
    </div>
</template>

<script>
import { url } from '@/configs/global'
export default {
    name: 'SystemTopbarUser',
    components: {  },
    data() {
        return {
            show: false
        }
    },
    methods: {
        logout(){
            this.show = false
			this.$store.dispatch('login.logout')
            window.location.reload(true) // mesmo depois de fazer logout os dados continuavam carregados em próximo login ex: ativos
        },
        perfil(){
            this.show = false
            if (this.$route.path != '/sistema/usuario') this.$router.push({path: '/sistema/usuario'});
        },
        trocar(){
            this.show = false
            window.location.href = '/bemvindo'
            //this.$store.reset()
        },
        remover_workspace(){
            this.$store.dispatch('empresa.remove')
        }        
    },
    computed: {
        getUserPhoto(){
            return this.$store.getters['usuario.get'].photo
        },
        getUserName(){
            return this.$store.getters['usuario.get'].name
        },
        getUserEmail(){
            return this.$store.getters['usuario.get'].email
        },
		getImageURL(){
            let id = this.$store.getters['usuario.get'].ID
            if (id != null && id != undefined){
                return `${url}/usuario-figura?user_id=${id}`
            }else{
                return ""
            }
            
        },
        getUserEmpresa(){
            return this.$store.getters['empresa.get']
        }
    },
    mounted() {
        let view = document.querySelector('.app-view')
        if (view == null) return
        view.addEventListener('click', (e) => {
            if(! Array.from(document.querySelector('.user-dropdown').getElementsByTagName('*')).includes(e.target) ) {
                this.show = false
            }
        })
    },
}
</script>

<style scoped>
    .user-dropdown {
        position: relative;
        height: 50px;
        min-width: 200px;
    }
    .topbar-button {
        display: none;
    }
    .user-dropdown-button {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-direction: row;
        padding: 0 5px;
        height: 100%;
        color: var(--c3);
    }
    .user-dropdown-button-img{
        height: 80% !important;
        border-radius: 50px;
        margin-left: 10px;
        align-content: right;
    }
    .user-dropdown-button:hover {
        background-color: rgba(0, 0, 0, 0.1);
        cursor: pointer;
    }
    .user-dropdown-content {
        position: absolute;
        right: 0px;
        background-color: var(--c4);
        min-width: 200px;
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2); padding: 10px;
        z-index: 10;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        transition: visibility 0s, opacity 0.3s linear;
    }
    .perfil{
        display: flex;
        flex-direction: column;
    }
    #content-show {
        visibility: visible;
        opacity: 1;
    }
    #content-hide {
        visibility: hidden;
        opacity: 0;
    }
    .user-dropdown-content a {
        text-decoration: none;
        color: white;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .user-dropdown-content a:hover {
        text-decoration: none;
        background-color: rgba(0, 0, 0, 0.1);
        cursor: pointer;
    }
    .user-imagecard-img{
        height: 90%;
        margin: 0 10px;
        border-radius: 5px;
    }
</style>